<script>
    // Import components 
    import Divider from '@/components/template/Divider.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'Equipment',
        components: {
            Divider
        },
         created() {
            if (this.initialArmors.length === 0) {
                this.$store.dispatch('equipments/getInitialArmors')
            }
        },
        computed: {
            ...mapState({
                initialWeapons: state => state.equipments.initialWeapons,
                initialArmors: state => state.equipments.initialArmors,
                initialShields: state => state.equipments.initialShields,
                maxInitialWeapons: state => state.equipments.maxInitialWeapons,
                maxInitialArmor: state => state.equipments.maxInitialArmor,
                maxInitialShield: state => state.equipments.maxInitialShield,
                equipmentInfo: state => state.equipments.equipmentInfo,
                equipments: state => state.charactersCreate.equipments,
                geo: state => state.charactersCreate.geo,
                skills: state => state.charactersCreate.skills
            }),
            shields() {
                if (this.skills.length > 0) {
                    for (let skill in this.skills) {
                        if (this.skills[skill].name == 'Combate' && this.skills[skill].specialization.name == 'Escudos') {
                            this.$store.dispatch('equipments/getInitialShields')
                            return true
                        }
                    }
                }
                return false
            },
            weapons() {
                if (this.skills.length > 0) {
                    const specializations = []

                    for (let skill in this.skills) {
                       if (this.skills[skill].name == 'Combate' && this.skills[skill].specialization.name != 'Escudos') {
                           specializations.push(this.skills[skill].specialization.id)
                       }
                    }

                    if (specializations.length > 0) {
                        this.$store.dispatch('equipments/getInitialWeapons', specializations)
                        return true
                    }
                }
                return false
            },
        },
        methods: {
            getEquipmentInfo(id, type) {
                this.$store.dispatch('equipments/getEquipmentInfo', {id, type})
            },
            chooseArmor(armor) {
                this.$store.dispatch('equipments/chooseArmor', armor)
            },
            chooseShield(shield) {
                this.$store.dispatch('equipments/chooseShield', shield)
            },
            chooseWeapon(weapon) {
                this.$store.dispatch('equipments/chooseWeapon', weapon)
            },
            increaseEquipment(equipment) {
                if (confirm('Deseja comprar 1 ' + equipment.name + '?')) {
                    this.$store.dispatch('equipments/increaseEquipment',  equipment)
                }
            },
            sellEquipment(equipment) {
                if (confirm('Deseja vender 1 ' + equipment.name + '?')) {
                    this.$store.dispatch('equipments/sellEquipment',  equipment)
                }
            }
        }
    }
</script>

