<template>
    <Topbar />

    <Notification />

    <InfoScreen />

    <router-view class="content" v-slot="{ Component }">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" appear>
            <component :is="Component" />
        </transition>
    </router-view>
    
    <Footer />
</template>

<script>
    import Topbar from '@/components/template/Topbar.vue'
    import Notification from '@/components/template/Notification.vue'
    import InfoScreen from '@/components/template/InfoScreen.vue'
    import Footer from '@/components/template/Footer.vue'
    
    export default {
        name: 'App',
        components: { 
            Topbar, Notification, InfoScreen, Footer
        },
        computed: {
            authenticate() {
                return this.$store.state.login.authenticate
            }
        },
        watch: {
            authenticate() {
                this.$router.push("/")
            }
        }
    }
</script>

