<template>
    <div>
        <Section name="Profile" />

        <Divider icon="account_circle" title="Alterar Senha" />

        <Form @submit="onSubmit">
            <div class="form-item">
                <label>Nova Senha</label>
                <Field name="password" type="password" :rules="{ required: true, min:8 }" label="Nova Senha" v-model="password" />
                <ErrorMessage name="password" />
            </div>

            <div class="form-item">
                <label>Confirmar Nova Senha</label>
                <Field name="passwordConfirmation" type="password" :rules="{ required: true, min:8, confirmed:password }" label="Confirmar Nova Senha" v-model="passwordConfirmation" />
                <ErrorMessage name="passwordConfirmation" />
            </div>

            <button>Alterar Senha</button>
        </Form>
    </div>
</template>

<script>
    // Import components
    import Section from '@/components/template/Section.vue'
    import Divider from '@/components/template/Divider.vue'
    import api from "@/utils/axios";
    
    // Import packages
    import { mapState } from 'vuex'
    import { Field, Form, ErrorMessage } from 'vee-validate';

    export default {
        name: 'Profile',
        components: {
            Section, Divider, Field, Form, ErrorMessage
        },
        data() {
            return {
                password: '',
                passwordConfirmation: ''
            }
        },
        computed: {
            ...mapState({
                username: state => state.login.userId,
            })
        },
        methods: {
            async onSubmit(values, actions) {
                const data = {
                    username: this.username,
                    password: String(values.password),
                    password_confirmation: String(values.passwordConfirmation)
                }

                await api.post('/change-password/', data)
                    .then(response => {
                        if (response.status === 200) {
                            const notification = {
                                message: 'Senha alterada com sucesso.',
                                type: 'success',
                                persistant: false
                            }

                            this.$store.dispatch('notifications/sendNotification', notification)
                        }
                    })
                    .catch(error => {
                        const notification = {
                            message: error,
                            type: 'error',
                            persistant: false
                        }

                        this.$store.dispatch('notifications/sendNotification', notification)
                    })

                actions.resetForm()
            }            
        }
    }
</script>

<style scoped></style>