<template>
    <div class="ability">
        <Divider :step=5 title="Escolha Habilidades" />

        <!-- Heritage Abilities -->
        <div class="paths" v-if="heritage.length != 0">
            <h3>{{ heritage.path.name }}</h3>
            <p>{{ heritage.path.description }}</p>
            <p class="path-info" v-if="maxHeritageAbilities > 0">Escolha <span>{{ maxHeritageAbilities }}</span> habilidades:</p>

            <div class="ability-grid">
                <div v-for="ability in heritageAbilities" :key="ability.id">
                    <div class="ability-item" v-if="ability.initial">
                        <p @click="heritageInitialAbility()">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else-if="maxHeritageAbilities > 0 || ability.trained == true">
                        <p @click="learnAbility(ability)">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else>
                        <p class="disable">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p class="disable">
                            <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                        </p>
                    </div>
                </div>
            </div> 
        </div>

        <!-- Source Abilities -->
        <div class="paths" v-if="source.length != 0">
            <h3>{{ source.path.name }}</h3>
            <p>{{ source.path.description }}</p>
            <p class="path-info" v-if="maxSourceAbilities > 0">Escolha <span>{{ maxSourceAbilities }}</span> habilidade:</p>

            <div class="ability-grid">
                <div v-for="ability in sourceAbilities" :key="ability.id">
                    <div class="ability-item" v-if="maxSourceAbilities > 0 || ability.trained == true">
                        <p @click="learnAbility(ability)">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else>
                        <p class="disable">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p class="disable">
                            <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                        </p>
                    </div>
                </div>
            </div> 
        </div>

        <!-- General / Magical Abilities  -->
        <div class="paths">
            <h3>Escolha um caminho de habilidades gerais</h3>

            <div class="form-item">
                <Field name="pathId" as="select" :rules="{ required: true }" label="Habilidades Gerais" v-model="pathId" @change="selectPath">
                <option></option>
                <option v-for="path in pathList" :key="path.id" :value="path.id">{{ path.name }}</option>
                </Field>
                <ErrorMessage name="pathId" />
            </div>

            <div class="paths" v-if="pathSelected.length > 0">
                <div v-for="path in pathSelected" :key="path.id">
                    <p><strong>{{ path.name }}</strong></p>
                    <p>{{ path.description }}</p>
                    <p class="path-info" v-if="maxPathAbilities > 0">Escolha <span>{{ maxPathAbilities }}</span> habilidade:</p>

                    <div class="ability-grid">
                        <div v-for="ability in path.abilities" :key="ability.id">
                            <div class="ability-item" v-if="maxPathAbilities > 0 || ability.trained == true">
                                <p @click="learnAbility(ability)">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p @click="showAbilityInfo(ability.id)">
                                    <span class="material-icons info">info</span>
                                </p>
                            </div>

                            <div class="ability-item" v-else>
                                <p class="disable">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p class="disable">
                                    <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                                </p>
                            </div>
                        </div>
                    </div> 
                </div>
            </div>

            <h3>Escolha um caminho de habilidades mágicas</h3>

            <div class="form-item">
                <Field name="magicPath" as="select" :rules="{ required: true }" label="Habilidades Mágicas" v-model="magicPathId" @change="selectMagicPath">
                    <option></option>
                    <option v-for="path in magicPathList" :key="path.id" :value="path.id">{{ path.name }}</option>
                </Field>
                <ErrorMessage name="magicPath" />
            </div>
            
            <div class="paths" v-if="magicPathSelected.length > 0">
                <div v-for="path in magicPathSelected" :key="path.id">
                    <p><strong>{{ path.name }}</strong></p>
                    <p>{{ path.description }}</p>
                    <p class="path-info" v-if="maxPathMagicAbilities > 0">Escolha <span>{{ maxPathMagicAbilities }}</span> habilidade:</p>

                    <div class="ability-grid">
                        <div v-for="ability in path.abilities" :key="ability.id">
                            <div class="ability-item" v-if="maxPathMagicAbilities > 0 || ability.trained == true">
                                <p @click="learnAbility(ability)">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p @click="showAbilityInfo(ability.id)">
                                    <span class="material-icons info">info</span>
                                </p>
                            </div>

                            <div class="ability-item" v-else>
                                <p class="disable">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p class="disable">
                                    <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                                </p>
                            </div>
                        </div>
                    </div> 
                </div>
            </div> 
        </div>
    </div>
</template>

<script>
    // Import components 
    import Divider from '@/components/template/Divider.vue'
    
    // Import packages 
    import { mapState } from 'vuex'
    import { Field, ErrorMessage } from 'vee-validate';

    export default {
        name: 'Abilities',
        components: {
            Divider, Field, ErrorMessage
        },
        created() {
            if (this.pathList.length === 0) {
                this.$store.dispatch('abilities/getPaths')
            }

            if (this.magicPathList.length === 0) {
                this.$store.dispatch('abilities/getMagicPaths')
            }            
        },
        data() {
            return {
                pathId: 0,
                magicPathId: 0     
            }
        },
        computed: {
            ...mapState({
                pathList: state => state.abilities.pathList,
                magicPathList: state => state.abilities.magicPathList,
                heritage: state => state.charactersCreate.heritage,
                heritageAbilities: state => state.charactersCreate.heritageAbilities,
                source: state => state.charactersCreate.source,
                sourceAbilities: state => state.charactersCreate.sourceAbilities,          
                pathSelected: state => state.abilities.pathSelected,
                magicPathSelected: state => state.abilities.magicPathSelected,
                abilityInfo: state => state.abilities.abilityInfo,
                maxHeritageAbilities: state => state.abilities.maxHeritageAbilities,
                maxSourceAbilities: state => state.abilities.maxSourceAbilities,
                maxPathAbilities: state => state.abilities.maxPathAbilities,
                maxPathMagicAbilities: state => state.abilities.maxPathMagicAbilities
            })
        },
        methods: {
            heritageInitialAbility() {
                alert('Essa habilidade não pode ser destreinada, ela foi herdada pela sua herança.')
            }, 
            learnAbility(ability) {
                this.$store.dispatch('abilities/learnAbility', ability)
            },
            selectPath() {
                if(this.pathId > 0) {
                    this.$store.dispatch('abilities/selectPath', this.pathId)
                } else {
                    this.$store.dispatch('abilities/clearSelectedPath')
                }
            },
            selectMagicPath() {
                if(this.magicPathId > 0) {
                    this.$store.dispatch('abilities/selectPath', this.magicPathId)
                } else {
                    this.$store.dispatch('abilities/clearSelectedMagicPath')
                }
                
            },
            showAbilityInfo(id) {
                this.$store.dispatch('abilities/getAbilityInfo', id)            
            }
        }
    } 
</script>

