import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    initialArmors: [],
    initialShields: [],
    initialWeapons: [],
    maxInitialWeapons: 2,
    maxInitialArmor: 1,
    maxInitialShield: 1,
    equipmentList: [],
    equipmentListSelected: false,
    equipmentListInfo: []
})

const getters = {

}

const actions = {
    async getEquipmentInfo ({ commit }, equipment) {
        const getEquipment = await api.get('/' + equipment.type + '/?id=' + equipment.id)
        commit('setEquipmentInfo', getEquipment.data.results)
    },
    async getInitialArmors ({ commit }) {
        const initialArmors = await api.get('/armors/?initial=true')
        commit('setInitialArmors', initialArmors.data.results)
    },
    async getInitialShields ({ commit }) {
        const shields = await api.get('/shields/')
        commit('setInitialShields', shields.data.results)
    },
    async getInitialWeapons ({ commit }, specializations) {
        let weapons = []

        for (let specialization in specializations) {
            const weapon = await api.get('/weapons/?specialization=' +specializations[specialization])
            weapons.push(weapon.data.results)
        }
        
        commit('setInitialWeapons', weapons)
    },
    chooseArmor({ commit }, armor) {
        commit('setChooseArmor', armor)
    },
    chooseShield({ commit }, shield) {
        commit('setChooseShield', shield)
    },
    chooseWeapon({ commit }, weapon) {
        commit('setChooseWeapon', weapon)
    },
    async increaseEquipment ({ commit }, equipment) {
        const getEquipment = await api.get('/' + equipment.type + '/?id=' + equipment.id)
        const equipmentResult = getEquipment.data.results
        commit('setIncreaseEquipment', { equipment, equipmentResult })
    },
    async sellEquipment ({ commit }, equipment) {
        const getEquipment = await api.get('/' + equipment.type + '/?id=' + equipment.id)
        const equipmentResult = getEquipment.data.results
        commit('setSellEquipment', { equipment, equipmentResult })
    },
    async getEquipmentList ({ commit }, equipmentCategory) {
        const equipment = await api.get('/' + equipmentCategory + '/')
        commit('setEquipmentList', equipment.data.results)
    },
    getEquipmentState ({ commit }, equipmentState) {
        commit('setEquipmentListSelected', equipmentState)
    },
    async getEquipmentListInfo ({ commit }, {id, equipmentCategory}) {
        const equipmentInfo = await api.get('/' + equipmentCategory + '/?id=' +id)
        commit('setEquipmentListInfo', equipmentInfo.data.results)
    },
    clearEquipmentListInfo ({ commit }) {
        commit('setClearEquipmentListInfo')
    },
    equipmentBuy ({ commit }, values) {
        commit('setEquipmentBuy', values)
    }
}

const mutations = {
    setEquipmentInfo (state, getEquipment) {
        if (store.state.infoScreen.id === getEquipment[0].id) {
            store.state.infoScreen.id = 0
            store.state.infoScreen.info = []
            store.state.infoScreen.show = false
            store.state.infoScreen.type = ''
        } else {
            store.state.infoScreen.id = getEquipment[0].id
            store.state.infoScreen.info = getEquipment[0]
            store.state.infoScreen.show = true
            store.state.infoScreen.type = 'equipment'
        }
    },
    setInitialArmors (state, initialArmors) {
        let armorList = []

        for (let armor in initialArmors) {
            const newArmor = {
                armor_penality: initialArmors[armor].armor_penality,
                cost: initialArmors[armor].cost,
                damage_resistance: initialArmors[armor].damage_resistance,
                description: initialArmors[armor].description,
                id: initialArmors[armor].id,
                initial: initialArmors[armor].initial,
                material: initialArmors[armor].material,
                name: initialArmors[armor].name,
                traits: initialArmors[armor].traits,
                weight: initialArmors[armor].weight,
                weight_stack: initialArmors[armor].weight_stack,
                trained: false
            }

            armorList.push(newArmor)
        }

        state.initialArmors = armorList
    },
    setInitialShields (state, shields) {
        let shieldList = []

        for (let shield in shields) {
            const newShield = {
                armor_penality: shields[shield].armor_penality,
                cost: shields[shield].cost,
                damage_resistance: shields[shield].damage_resistance,
                defense_bonus: shields[shield].defense_bonus,
                description: shields[shield].description,
                id: shields[shield].id,
                life_points: shields[shield].life_points,
                name: shields[shield].name,
                traits: shields[shield].traits,
                weight: shields[shield].weight,
                weight_stack: shields[shield].weight_stack,
                trained: false
            }

            shieldList.push(newShield)
        }

        state.initialShields = shieldList
    },
    setInitialWeapons (state, weapons) {        
        let weaponList = []
        
        for (let groups in weapons) {
            const weaponGroup = weapons[groups]

            for (let weapon in weaponGroup) {
                const newWeapon = {
                    body: weaponGroup[weapon].body,
                    cost: weaponGroup[weapon].cost,
                    damage: weaponGroup[weapon].damage,
                    description: weaponGroup[weapon].description,
                    id: weaponGroup[weapon].id,
                    name: weaponGroup[weapon].name,
                    precision: weaponGroup[weapon].precision,
                    range: weaponGroup[weapon].range,
                    specialization: weaponGroup[weapon].specialization,
                    traits: weaponGroup[weapon].traits,
                    weight: weaponGroup[weapon].weight,
                    weight_stack: weaponGroup[weapon].weight_stack,
                    acquired: false
                }

                weaponList.push(newWeapon)
            }
        }
        
        state.initialWeapons = weaponList
    },
    setChooseArmor (state, armor) {
        let armorList = state.initialArmors

        armorList.map(armorItem => {
            if (armorItem.id == armor.id) {
                armorItem.acquired = !armorItem.acquired

                const armorList = store.state.charactersCreate.equipments.armors
                const filter = armorList.find(equipment => equipment.id === armor.id)

                if (armorItem.acquired) {
                    state.maxInitialArmor = state.maxInitialArmor -1
                    
                    if (!filter) {
                        const newArmor = {
                            id: armor.id,
                            name: armor.name,
                            type: "armors",
                            quantity: 1,
                            initial: true
                        }
    
                        store.state.charactersCreate.equipments.armors.push(newArmor) 
                    } 
                } else {
                    state.maxInitialArmor = state.maxInitialArmor +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        armorList.splice(armorList.findIndex(index => index.id === armor.id),1) 
                    }
                      
                }
            }
            return armorItem
        })
    },
    setChooseShield (state, shield) {
        let shieldList = state.initialShields

        shieldList.map(shieldItem => {
            if (shieldItem.id == shield.id) {
                shieldItem.acquired = !shieldItem.acquired

                const shieldList = store.state.charactersCreate.equipments.shields
                const filter = shieldList.find(equipment => equipment.id === shield.id)

                if (shieldItem.acquired) {
                    state.maxInitialShield = state.maxInitialShield -1                    

                    if (!filter) {
                        const newShield = {
                            id: shield.id,
                            name: shield.name,
                            type: "shields",
                            quantity: 1,
                            initial: true
                        }
    
                        store.state.charactersCreate.equipments.shields.push(newShield) 
                    }
                } else {
                    state.maxInitialShield = state.maxInitialShield +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        shieldList.splice(shieldList.findIndex(index => index.id === shield.id),1) 
                    }
                }
            }
            return shieldItem
        })
    },
    setChooseWeapon (state, weapon) {
        let weaponList = state.initialWeapons

        weaponList.map(weaponItem => {
            if (weaponItem.id == weapon.id) {
                weaponItem.acquired = !weaponItem.acquired

                const weaponList = store.state.charactersCreate.equipments.weapons
                const filter = weaponList.find(equipment => equipment.id === weapon.id)

                if (weaponItem.acquired) {
                    state.maxInitialWeapons = state.maxInitialWeapons -1 

                    if (!filter) {
                        const newWeapon = {
                            id: weapon.id,
                            name: weapon.name,
                            type: "weapons",
                            quantity: 1,
                            initial: true
                        }
    
                        store.state.charactersCreate.equipments.weapons.push(newWeapon) 
                    }
                } else {
                    state.maxInitialWeapons = state.maxInitialWeapons +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        weaponList.splice(weaponList.findIndex(index => index.id === weapon.id),1)   
                    }                    
                }
            }
            return weaponItem
        })
    },
    setIncreaseEquipment (state, { equipment, equipmentResult } ) {
        const category = store.state.charactersCreate.equipments[equipment.type]

        const findEquipment = category.find(el => {
            if (el.id === equipment.id) {
                return el
            }
        })

        if (findEquipment) {
            if (store.state.charactersCreate.geo >= equipmentResult[0].cost) {
                findEquipment.quantity = findEquipment.quantity +1
                
                store.state.charactersCreate.geo = store.state.charactersCreate.geo - equipmentResult[0].cost
            
                store.state.notifications.message = 'Você comprou 1 ' + equipment.name + '.'
                store.state.notifications.type = 'success'
                store.state.notifications.show = true
                store.state.notifications.persistant = false
            } else {
                console.log('else')
                store.state.notifications.message = 'Você não possui G$' + equipmentResult[0].cost + ' para fazer essa compra.'
                store.state.notifications.type = 'error'
                store.state.notifications.show = true
                store.state.notifications.persistant = false
            }
        }
    },
    setSellEquipment (state, { equipment, equipmentResult } ) {
        const category = store.state.charactersCreate.equipments[equipment.type]

        const findEquipment = category.find(el => {
            if (el.id === equipment.id) {
                return el
            }
        })

        if (findEquipment.initial) {
            if (findEquipment.quantity > 1) {
                store.state.charactersCreate.geo = store.state.charactersCreate.geo + equipmentResult[0].cost
            } else {
                findEquipment.acquired = false
                              
                if (equipment.type === 'armors') {
                    const findIndex = state.initialArmors.findIndex(index => index.id === equipment.id)
                    state.initialArmors[findIndex].acquired = false
                    state.maxInitialArmor = state.maxInitialArmor +1
                } else if (equipment.type === 'shields') {
                    const findIndex = state.initialShields.findIndex(index => index.id === equipment.id)
                    state.initialShields[findIndex].acquired = false
                    state.maxInitialShield = state.maxInitialShield +1
                } else if (equipment.type === 'weapons') {
                    const findIndex = state.initialWeapons.findIndex(index => index.id === equipment.id)
                    state.initialWeapons[findIndex].acquired = false
                    state.maxInitialWeapons = state.maxInitialWeapons +1
                }      
            }
        } else {
            store.state.charactersCreate.geo = store.state.charactersCreate.geo + equipmentResult[0].cost
        }

        if (findEquipment) { 
            findEquipment.quantity = findEquipment.quantity -1

            store.state.notifications.message = 'Você vendeu 1 ' + equipment.name + '.'
            store.state.notifications.type = 'success'
            store.state.notifications.show = true
            store.state.notifications.persistant = false
            
            if (findEquipment.quantity === 0) {
                category.splice(category.findIndex(index => index.id === equipment.id),1)

                store.state.notifications.message = 'O item ' + equipment.name + ' foi removido do seu inventário.'
                store.state.notifications.type = 'success'
                store.state.notifications.show = true
                store.state.notifications.persistant = false
            }
        }
    },
    setEquipmentList(state, equipment) {
        state.equipmentList = equipment
    },
    setEquipmentListSelected (state, equipmentState) {
        state.equipmentListSelected = equipmentState
    },
    setEquipmentListInfo (state, equipmentInfo) {
        state.equipmentListInfo = equipmentInfo
    },
    setClearEquipmentListInfo (state) {
        state.equipmentListInfo = []
    },
    setEquipmentBuy (state, values) {
        const newEquipment = {
            id: values.equipment.id,
            name: values.equipment.name,
            type: values.equipmentCategory,
            quantity: 1
        }

        if (store.state.charactersCreate.equipments[values.equipmentCategory].length > 0) {
            const category = store.state.charactersCreate.equipments[values.equipmentCategory]
            
            const findEquipment = category.find(el => {
                if (el.id === values.equipment.id) {
                    return el
                }
            })

            if (findEquipment) { 
                findEquipment.quantity = findEquipment.quantity +1
            } else {
                store.state.charactersCreate.equipments[values.equipmentCategory].push(newEquipment)
            }

            store.state.notifications.message = 'Você comprou ' + newEquipment.name + '.'
            store.state.notifications.type = 'success'
            store.state.notifications.show = true
            store.state.notifications.persistant = false
        } else {
            store.state.charactersCreate.equipments[values.equipmentCategory].push(newEquipment)
            
            store.state.notifications.message = 'Você comprou ' + newEquipment.name + '.'
            store.state.notifications.type = 'success'
            store.state.notifications.show = true
            store.state.notifications.persistant = false
        }

        store.state.charactersCreate.geo = store.state.charactersCreate.geo - values.equipment.cost
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}