import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    sourceList: [],  
    sourceInfo: [],
    sourceSelected: false
})

const getters = {}

const actions = {
    async getSourcesList ({ commit }) {
        const sources = await api.get('/sources/?fields=id,name')
        commit('setSourcesList', sources.data.results)
    },
    async getSourceInfo ({ commit }, id) {
        const sourceInfo = await api.get('/sources/?id=' + id)
        commit('setSourceInfo', sourceInfo.data.results)
    },
    getSourceState ({ commit }, sourceState) {
        commit('setSourceSelected', sourceState)
    },
    getCharacterSource ({ commit }, values) { 
        commit('setCharacterSource', values)
    }
}

const mutations = {
    setSourcesList (state, sources) {
        state.sourceList = sources
    },
    setSourceInfo (state, sourceInfo) {
        state.sourceInfo = sourceInfo
    },
    setSourceSelected (state, sourceState) {
        state.sourceSelected = sourceState
    },
    setCharacterSource (state, values) {
        // Source
        store.state.charactersCreate.source = values.source

        // Source Abilities
        store.state.charactersCreate.sourceAbilities = []

        for (let ability in values.source.path.abilities) {
            const sourceAbility = values.source.path.abilities[ability]

            const trainedAbility = {
                activation: sourceAbility.activation,
                cost: sourceAbility.cost,
                description: sourceAbility.description,
                id: sourceAbility.id,
                initial: sourceAbility.initial,
                name: sourceAbility.name,
                requirement: sourceAbility.requirement,
                resource: sourceAbility.resource,
                sorcery: sourceAbility.sorcery,
                traits: sourceAbility.traits,
                trained: false,
                source: sourceAbility.source,
                path: values.source.path.id,
            }

            store.state.charactersCreate.sourceAbilities.push(trainedAbility)
        }

        // Source Skills

        let setPrimarySpecialization = []

        const primarySkillSpecialization = values.source.primary_skill.specialization

        for (let specialization in primarySkillSpecialization) {
            const specializationId = primarySkillSpecialization[specialization].id
            
            if (specializationId == values.primarySpecialization) {
                setPrimarySpecialization = primarySkillSpecialization[specialization]
                store.state.charactersCreate.specializations.push(setPrimarySpecialization)
            }
        }

        let primarySkillId = ''

        if (setPrimarySpecialization.length === 0) {
            primarySkillId = values.source.primary_skill.id
        } else {
            primarySkillId = values.source.primary_skill.id + '-' + setPrimarySpecialization.id
        }

        const primarySkill = {
            id: primarySkillId,
            name: values.source.primary_skill.name,
            attribute: values.source.primary_skill.attribute,
            attribute_label: values.source.primary_skill.attribute_label,
            affinity: values.source.primary_skill.affinity,
            affinity_label: values.source.primary_skill.affinity_label,
            description: values.source.primary_skill.description,
            specialization: setPrimarySpecialization,
            actions: values.source.primary_skill.actions,
            traits: values.source.primary_skill.traits,
            armor_penality: values.source.primary_skill.armor_penality,
            training_level: 1,
            source_skill: true  
        }

        let setSecondarySpecialization = []

        const secondarySkillSpecialization = values.source.secondary_skill.specialization

        for (let specialization in secondarySkillSpecialization) {
            const specializationId = secondarySkillSpecialization[specialization].id
            
            if (specializationId == values.secondarySpecialization) {
                setSecondarySpecialization = secondarySkillSpecialization[specialization]
                store.state.charactersCreate.specializations.push(setSecondarySpecialization)
            }
        }

        let secondarySkillId = ''

        if (setSecondarySpecialization.length === 0) {
            secondarySkillId = values.source.secondary_skill.id
        } else {
            secondarySkillId = values.source.secondary_skill.id + '-' + setSecondarySpecialization.id
        }       

        const secondarySkill = {
            id: secondarySkillId,
            name: values.source.secondary_skill.name,
            attribute: values.source.secondary_skill.attribute,
            attribute_label: values.source.secondary_skill.attribute_label,
            affinity: values.source.secondary_skill.affinity,
            affinity_label: values.source.secondary_skill.affinity_label,
            description: values.source.secondary_skill.description,
            specialization: setSecondarySpecialization,
            actions: values.source.secondary_skill.actions,
            traits: values.source.secondary_skill.traits,
            armor_penality: values.source.secondary_skill.armor_penality,
            training_level: 1,
            source_skill: true  
        }

        const checkSourceSkills = store.state.charactersCreate.skills.filter(skill => skill.source_skill === true)
        const checkPrimarySkill = store.state.charactersCreate.skills.filter(skill => skill.id === primarySkill.id)
        const checkSecondarySkill = store.state.charactersCreate.skills.filter(skill => skill.id === secondarySkill.id)

        if (checkSourceSkills.length > 0) {
            for (let sourceSkill in checkSourceSkills) {
                const skills = store.state.charactersCreate.skills
                skills.splice(skills.findIndex(index => index.name === checkSourceSkills[sourceSkill].name),1)   

                store.state.charactersCreate.skillDetail = []
            }
        }  
        
        if (checkPrimarySkill.length > 0) {
            const skills = store.state.charactersCreate.skills
            skills.splice(skills.findIndex(index => index.name === checkPrimarySkill.name),1)
            store.state.charactersCreate.spentSkillPoints = store.state.charactersCreate.spentSkillPoints - checkPrimarySkill[0].training_level

        } 
        
        if (checkSecondarySkill.length > 0) {
            const skills = store.state.charactersCreate.skills
            skills.splice(skills.findIndex(index => index.id === checkSecondarySkill.name),1)
            store.state.charactersCreate.spentSkillPoints = store.state.charactersCreate.spentSkillPoints - checkSecondarySkill[0].training_level            
        }

        store.state.charactersCreate.skills.push(primarySkill)
        store.state.charactersCreate.skills.push(secondarySkill)
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}