const state = () => ({
    message: '',
    type: '',
    show: false,
    persistant: false
})

const getters = {}

const actions = {
    sendNotification ({ commit }, notification) {
        commit('setSendNotification', notification)
    },
    clearNotification ({ commit }) {
        commit('setClearNotification')
    }
}

const mutations = {
    setSendNotification (state, notification) {
        state.message = notification.message
        state.type = notification.type
        state.show = true
        state.persistant = notification.persistant
    },
    setClearNotification (state) {
        state.message = '',
        state.type = '',
        state.show = false,
        state.persistant = false
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}