import { defineRule, configure } from 'vee-validate';
import { confirmed, is, is_not, min, required } from '@vee-validate/rules';
import { localize } from '@vee-validate/i18n';

const rules = () => {
    defineRule('confirmed', confirmed);
    defineRule('is', is);
    defineRule('is_not', is_not)
    defineRule('min', min);
    defineRule('required', required);

    configure({
        generateMessage: localize('pt-BR', {
            messages: {
                confirmed: 'O valor do campo {field} não é igual ao campo comparado.',
                is: 'Você ainda tem que escolher {field}.',
                is_not: 'Você ainda tem que escolher sua {field}.',
                min: 'O campo {field} tem o tamanho mínimo de 8 caracteres.',
                required: 'O campo {field} é obrigatório.'
            },
        })
    })
}

export default rules