<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Escolha sua Herança" />

        <div class="heritages-row">
            <div class="heritages-choice">
                <div class="heritages-card" v-for="heritage in heritagesList" :key="heritage.id" @click="getHeritageInfo(heritage.id)">
                    <h2>{{ heritage.name }}</h2>
                </div>
            </div>
            
            <div class="heritages-info" v-show="heritageSelected == true">
                <div v-for="heritage in heritageInfo" :key="heritage.id">
                    <h2>{{ heritage.name }}</h2>

                    <p><strong>Aparência.</strong> {{ heritage.appearance }}</p>

                    <p><strong>Sociedade.</strong> {{ heritage.society }}</p>

                    <p><strong>Tamanho.</strong> {{ heritage.size }}</p>

                    <!-- <p><strong>Deslocamento.</strong> {{ heritage.move }} + <span class="attribute">Agilidade</span></p> -->

                    <button @click.prevent="heritageSave(heritage)">Escolher {{ heritage.name }}</button>
                </div>
            </div>

            <div class="heritages-info" v-show="heritageSelected == false">   
                <h2>Heranças</h2>

                <p>As Heranças são o maior presente que recebemos para Etheria, dentre as diversas criaturas que povoam o continente, algumas criaturas foram abençoadas com uma grande afinidade elemental.</p>
                <p>Escolher uma herança é como colocar uma lente, ela determina como você vê o mundo e entende as culturas a sua volta.</p>
                
                <h3>Características das Heranças</h3>

                <ul>
                    <li><strong>Habilidades.</strong> Toda herança possui um caminho de habilidades exclusivo chamado Ancestralidade. Você sempre começa com a primeira habilidade daquele caminho e pode escolher (+2) habilidades da lista.</li>
                    <li><strong>Tamanho.</strong> Sua herança define seu tamanho. Veja mais sobre isso na página X.</li>
                    <li><strong>Deslocamento.</strong> Seu deslocamento define quantos metros você anda quando usa uma ação de Mover, esse deslocamento leva em consideração somente deslocamento terrestre, outros tipos de deslocamento estão especificados na descrição de sua respectiva herança.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    // Import components 
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Section from '@/components/template/Section.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'HeritagesScreen',
        components: {
            ChoiceScreen, Section
        },
        created() {
            if (this.heritagesList.length === 0) {
                this.$store.dispatch('heritages/getHeritagesList')
            }      
        },
        computed: {
            ...mapState({
                heritageInfo: state => state.heritages.heritageInfo,
                heritageSelected: state => state.heritages.heritageSelected,
                heritagesList: state => state.heritages.heritagesList,
            })
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('heritages/getHeritageState', false)
                document.body.style.overflow = "visible";  
            },
            getHeritageInfo(id) {
                this.$store.dispatch('heritages/getHeritageInfo', id)
                this.$store.dispatch('heritages/getHeritageState', true)
            },
            heritageSave(heritage) {
                this.$store.dispatch('heritages/getCharacterHeritage', heritage)
                this.$router.push("/create-character")
            }
        }
    }
</script>

<style scoped>
    .heritages-row {
        align-items: flex-start;
        display: flex;
        width: 100%;
    }
    .heritages-choice {
        display: grid;
        grid-column-gap: 16px;
        grid-row-gap: 16px;
        grid-template-columns: repeat(3, 1fr);
        margin-right: 64px;
        width: 60%;
    }
    .heritages-card {
        align-items: center;
        cursor: pointer;
        border: 1px solid #333;
        display: flex;
        height: 100px;
        justify-content: center;
        width: 100%;

        -webkit-transition: background-color 0.5s ease-in-out;
        -moz-transition: background-color 0.5s ease-in-out;
        -ms-transition: background-color 0.5s ease-in-out;
        -o-transition: background-color 0.5s ease-in-out;
        transition: background-color 0.5s ease-in-out;
    }
    .heritages-card:hover {
        background-color: rgba(130, 117, 166, 0.1);
        border: 1px solid #8275A6;

        -webkit-transition: background-color 0.5s ease-in-out;
        -moz-transition: background-color 0.5s ease-in-out;
        -ms-transition: background-color 0.5s ease-in-out;
        -o-transition: background-color 0.5s ease-in-out;
        transition: background-color 0.5s ease-in-out;
    }
