<template>
    <div class="skills-row">
        <Divider v-if="spentSkillPoints < skillPoints" :step=4 title="Escolha Perícias" :button="true" label="Treinar novas Perícias" to="/create-character/skills" />
        <Divider v-else :step=4 title="Escolha Perícias" />

        <div class="skill-points">
            <p>Pontos de Perícia {{skillPoints - spentSkillPoints}} / {{skillPoints}}</p>
        </div>

        <SkillElement />
    </div>

</template>

<script>
    // Import components
    import Divider from '@/components/template/Divider.vue'
    import SkillElement from '@/components/characters/SkillElement.vue'

    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'Skills',
        components: {
            Divider, SkillElement
        },
        computed: {
            ...mapState({
                mind: state => state.charactersCreate.mind,
                cunning: state => state.charactersCreate.cunning,
                spentSkillPoints: state => state.charactersCreate.spentSkillPoints
            }),
            skillPoints() {
                return (this.mind *5) + this.cunning
            }
        }
    }
</script>

<style scoped>
    .skills-row {
        margin-bottom: 32px;
        width: 100%;
    }
    .skill-points {
        display: flex;
        justify-content: end;
