import api from "@/utils/axios";

const state = () => ({
    loadingCharacterList: true,
    loadingShowCharacter: true,
    characterList: [],
    showCharacter: []
})

const getters = {}

const actions = {
    async getCharacterList ({ commit }, userId) {
        const character = await api.get('/characters/?username=' +userId)
        commit('setCharacterList', character.data.results)
    },
    async deleteCharacter ({ commit }, id) {
        await api.delete('/characters/' + id + '/')
        const characters = await api.get('/characters/')
        commit('setDeleteCharacter', characters)
    },
    async showCharacter ({ commit }, id) {
        const character = await api.get('/characters/?id=' +id)
        const gender = await api.get('/genders/?id=' +character.data.results[0].gender)
        const heritage = await api.get('/heritages/?id=' +character.data.results[0].heritage)
        const source = await api.get('/sources/?id=' +character.data.results[0].source)
        const motivation = await api.get('/motivations/?id=' +character.data.results[0].motivation)  

        const characterData = {
            character: character.data.results[0],
            gender: gender.data.results[0],
            heritage: heritage.data.results[0],
            source: source.data.results[0],
            motivation: motivation.data.results[0],
            skills: JSON.parse(character.data.results[0].skills),
            abilities: JSON.parse(character.data.results[0].abilities),
            equipments: JSON.parse(character.data.results[0].equipments),
            spells: JSON.parse(character.data.results[0].spells),
            enchants: JSON.parse(character.data.results[0].enchants)
        }

        commit('setShowCharacter', characterData)
    }
}

const mutations = {
    setCharacterList (state, characters) {
        state.characterList = characters
        
        setTimeout(() => {
            state.loadingCharacterList = false
        }, 2000)
    },
    setDeleteCharacter (state, characters) {
        state.characterList = characters.data.results
    },
    setShowCharacter (state, characterData) {
        state.showCharacter = characterData

        setTimeout(() => {
            state.loadingShowCharacter = false
        }, 2000)
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}