const state = () => ({
    id: 0,
    info: '',
    show: false,
    type: ''
})

const getters = {}

const actions = {
    closeInfoScreen ({ commit }) {
        commit('setCloseInfoScreen')
    }
}

const mutations = {
    setCloseInfoScreen (state) {
        state.id = 0
        state.info = ''
        state.show = false
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}