<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Compre Equipamentos" />

        <h3 class="geo">Você tem <span>{{ geo }}</span> geo.</h3>

        <div class="form-item">
            <label>Escolha uma categoria de equipamento</label>
            <Field name="equipmentCategory" as="select" v-model="equipmentCategory">
                <option value=""></option>
                <option value="armors">Armaduras</option>
                <option value="weapons">Armas</option>
                <option value="shields">Escudos</option>
                <option value="items">Itens</option>
            </Field>
        </div>

        <div class="equipment-row">
            <div class="equipment-choice" v-if="equipmentCategory">
                <div class="equipment-choice-header">
                    <p>Nome</p>
                    <p>Geo (G$)</p>
                    <p>Carga</p>
                </div>

                <div class="equipment-choice-row" v-for="equipment in equipmentList" :key="equipment.id" @click="getEquipmentListInfo(equipment.id, equipmentCategory)">
                    <p>{{ equipment.name }}</p>
                    <p>G$ {{ equipment.cost }}</p>
                    <p v-if="equipment.weight_stack == 1">{{ equipment.weight }}</p>
                    <p v-else>0 ( {{ equipment.weight_stack }} )</p>
                </div>
            </div>

            <div class="equipment-info" v-if="equipmentListSelected">
                <div v-for="equipment in equipmentListInfo" :key="equipment.id">
                    <Form @submit="onSubmit">   
                        <div v-if="equipment.damage">
                            <h3>{{ equipment.name }}</h3>
                            <p><strong>Especialização.</strong> {{ equipment.specialization.name }}</p>
                            <p>{{ equipment.description }}</p>
                            <p><strong>Custo.</strong> G$ {{ equipment.cost }}</p>
                            
                            <p v-if="equipment.weight_stack == 1"><strong>Carga.</strong> {{ equipment.weight }}</p>
                            <p v-else><strong>Carga.</strong> 0 ( {{ equipment.weight_stack }} )</p>
                            
                            <p><strong>Corpo Mínimo.</strong> {{ equipment.body }}</p>
                            <p><strong>Alcance.</strong> {{ equipment.range }}</p>
                            <p><strong>Precisão.</strong> {{ equipment.precision }}</p>
                            <p><strong>Dano.</strong> {{ equipment.damage }}</p>
                        </div>

                        <div v-else>
                            <h3>{{ equipment.name }}</h3>
                            <p v-if="equipment.specialization"><strong>Grupo.</strong> {{ equipment.specialization.name}}</p>
                            <p>{{ equipment.description }}</p>
                            <p><strong>Custo.</strong> G$ {{ equipment.cost }}</p>
                            <p v-if="equipment.weight_stack == 1"><strong>Carga.</strong> {{ equipment.weight }}</p>
                            <p v-else><strong>Carga.</strong> 0 ( {{ equipment.weight_stack }} )</p>
                            <p v-if="equipment.defense_bonus > 0"><strong>Bônus de Defesa.</strong> {{ equipment.defense_bonus }}</p>
                            <p v-if="equipment.armor_penality "><strong>Penalidade de Armadura.</strong> {{ equipment.armor_penality }}</p>
                            <p v-if="equipment.damage_resistance"><strong>Resistência a Dano.</strong> {{ equipment.damage_resistance }}</p>
                            <p v-if="equipment.life_points > 0"><strong>Pontos de Vida.</strong> {{ equipment.life_points }}</p>
                            <p v-if="equipment.material"><strong>Material.</strong> {{ equipment.material.name }}</p>
                        </div>

                        <Field name="equipment" type="hidden" :value="equipment" />
                        <Field name="equipmentCategory" type="hidden" :value="equipmentCategory" />

                        <button  v-if="equipment.cost <= geo">Comprar {{ equipment.name }}</button>
                        <p v-else class="button-disable">Geo insuficiente</p>
                    </Form>    
                </div>
            </div>

            <div class="equipment-info" v-else>
                <h2>Equipamentos</h2>

                <p>
                    Aventureiros precisam de equipamentos, se envolver em aventuras audaciosas geralmente pode ser perigoso e carregar uma lâmina afiada, um arco de madeira confiável e uma armadura adequada para seu tamanho é imprescindível. Além disso, mesmo itens mais simples como cordas e utensílios de cozinha podem ser de grande ajuda em momentos de necessidade.
                </p>

                <h3>Características dos Equipamentos</h3>

                <ul>
                    <li><strong>Geo (G$).</strong> Todo equipamento tem um custo em G$.</li>
                    <li><strong>Carga.</strong> Valor de carga do item, para carga 0 o valor entre parênteses determina quantas unidades daquele item são necessárias para valer como 1 de carga.</li>
                    <li><strong>Outros.</strong> Dependendo do tipo de equipamento outras características são informadas.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    // Import components 
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Section from '@/components/template/Section.vue'

    // Import packages
    import { mapState } from 'vuex'
    import { Field, Form } from 'vee-validate';

    export default {
        name: 'EquipmentsScreen',
        components: {
            ChoiceScreen, Section, Field, Form
        },
        data() {
            return {
                equipmentCategory: ''
            }
        },
        computed: {
            ...mapState({
                equipmentList: state => state.equipments.equipmentList,
                equipmentListSelected: state => state.equipments.equipmentListSelected,
                equipmentListInfo: state => state.equipments.equipmentListInfo,
                geo: state => state.charactersCreate.geo
            })
        },
        watch: {
            equipmentCategory() {
                if (this.equipmentCategory != '') {
                    this.$store.dispatch('equipments/getEquipmentList', this.equipmentCategory)
                    this.$store.dispatch('equipments/getEquipmentState', false)
                    this.$store.dispatch('equipments/clearEquipmentListInfo')
                } else {
                    this.$store.dispatch('equipments/getEquipmentState', false)
                    this.$store.dispatch('equipments/clearEquipmentListInfo')                   
                }
            }
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('equipments/getEquipmentState', false)
                document.body.style.overflow = "visible";
                
            },
            getEquipmentListInfo(id, equipmentCategory) {
                this.$store.dispatch('equipments/getEquipmentListInfo', { id, equipmentCategory })
                this.$store.dispatch('equipments/getEquipmentState', true)
            },
            onSubmit(values) {
                if (confirm('Deseja comprar uma unidade desse equipamento?')) {
                    this.$store.dispatch('equipments/equipmentBuy', values)
                }                 
            }
        }
    }
</script>

