import api from "@/utils/axios";

const state = () => ({
    authenticate: false,
    unauthorized: false,
    username: '',
    userId: 0,
    token: ''
})

const getters = {}

const actions = {
    async authenticate({ commit }, values) {
        await api.post('/login/', values)
            .then(response => {
                if (response.status === 200) {
                    const login = {
                        username: values.username,
                        userId: response.data,
                        token: response.config.headers.Authorization
                    }
                    commit('setAuthenticate', login)
                }
            })
            .catch(error => {
                if (error) {
                    commit('setUnauthorized')
                }
            })
    },
    logout({ commit }) {
        commit('setLogout')
    }
}

const mutations = {
    setAuthenticate (state, login) {
        state.username = login.username
        state.userId = login.userId
        state.token = login.token
        state.authenticate = true
    },
    setUnauthorized (state) {
        state.unauthorized = true
    },
    setLogout (state) {
        state.authenticate = false
        state.unauthorized = false
        state.username = ''
        state.userId = 0
        state.token = ''
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}