<template>
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" appear>
        <div v-if="show" class="notification" :class="type">
            <p>
                <span v-if="type === 'success'" class="material-icons">check_circle_outlined</span>
                <span v-else class="material-icons">error_outlined</span>
                {{ message }}
            </p>

            <p v-if="persistant" class="notification-close" @click="clearNotification">
                <span class="material-icons">cancel</span>
            </p>
        </div>
    </transition>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: 'Notification',
        computed: {
            ...mapState({
                message: state => state.notifications.message,
                type: state => state.notifications.type,
                show: state => state.notifications.show,
                persistant: state => state.notifications.persistant
            })
        },
        methods: {
            clearNotification() {
                this.$store.dispatch('notifications/clearNotification')
            }
        },
        watch: {
            show() {        
                if (!this.persistant) {
                    setTimeout(() => {
                        this.$store.dispatch('notifications/clearNotification')
                    }, 4000)
                }
            }
        }
    }
</script>

<style scoped>
    .notification {
        align-items: center; 
        background-color: #fff;
        display: flex;
        justify-content: space-between;
        padding: 32px;
        position: fixed;
        right: 0;
        top: 0;
        width: 40%;
        margin: 32px 32px 0 0;
        z-index: 99999;

        -webkit-box-shadow: 1px 1px 4px 2px #d5d5d5;
        box-shadow: 1px 1px 4px 2px #d5d5d5;
    }
    .notification p {
        align-items: center;
        display: flex;
    }
    .notification.success {
        color: #0f5132;
        background-color: #d1e7dd;

        -webkit-box-shadow: 1px 1px 4px 2px #badbcc;
        box-shadow: 1px 1px 4px 2px #badbcc;
    }
    .notification.error {
        color: #842029;
        background-color: #f8d7da;

        -webkit-box-shadow: 1px 1px 4px 2px #f5c2c7;
        box-shadow: 1px 1px 4px 2px #f5c2c7;
    }
    .notification-close {
        cursor: pointer;
    }
</style>