import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    pathList: [],
    magicPathList: [],
    pathSelected: [],
    magicPathSelected: [],
    abilityInfo: [],
    maxHeritageAbilities: 2,
    maxSourceAbilities: 1,
    maxPathAbilities: 1,
    maxPathMagicAbilities: 1
})

const getters = {

}

const actions = {
    async getAbilityInfo ({ commit }, id) {
        const ability = await api.get('/abilitys/?id=' + id)
        commit('setAbilityInfo', ability.data.results)
    },
    async getPaths ({ commit }) {
        const paths = await api.get('/paths/?source=3')
        commit('setPaths', paths.data.results)
    },
    async getMagicPaths ({ commit }) {
        const magicPaths = await api.get('/paths/?source=4')
        commit('setMagicPaths', magicPaths.data.results)
    },
    async selectPath ({ commit }, id) {
        const path = await api.get('/paths/?id=' + id)

        if (path.data.results[0].source.name == 'Geral') {
            commit('setSelectPath', path.data.results)
        } else {
            commit('setSelectMagicPath', path.data.results)
        }
    },
    clearSelectedPath ({ commit }) {
        commit('setClearSelectedPath')
    },
    clearSelectedMagicPath ({ commit }) {
        commit('setClearSelectedMagicPath')
    },
    learnAbility({ commit }, ability) {
        commit('setAbility', ability)
    },
}

const mutations = {
    setAbilityInfo (state, ability) {
        if (store.state.infoScreen.id === ability[0].id) {
            store.state.infoScreen.id = 0
            store.state.infoScreen.info = []
            store.state.infoScreen.show = false
            store.state.infoScreen.type = ''
        } else {
            store.state.infoScreen.id = ability[0].id
            store.state.infoScreen.info = ability[0]
            store.state.infoScreen.show = true
            store.state.infoScreen.type = 'ability'
        }
    },
    setPaths (state, paths) {
        state.pathList = paths
    },
    setMagicPaths (state, magicPaths) {
        state.magicPathList = magicPaths
    },
    setSelectPath (state, path) {
        if (state.pathSelected.length > 0) {
            state.pathSelected = []
            store.state.charactersCreate.abilities = []

            if (state.maxPathAbilities === 0) {
                state.maxPathAbilities = state.maxPathAbilities +1
            }   
        }
        state.pathSelected = path
        store.state.charactersCreate.sorcery = false
        store.state.charactersCreate.spells = []    
        store.state.charactersCreate.maxSpells = 3
    },
    setClearSelectedPath (state) {
        state.pathSelected = []
        store.state.charactersCreate.abilities = []
        store.state.charactersCreate.sorcery = false
        store.state.charactersCreate.enchants = []
        store.state.charactersCreate.spells = []
        
        if (state.maxPathAbilities === 0) {
            state.maxPathAbilities = state.maxPathAbilities +1
        }  
    },
    setSelectMagicPath (state, magicPath) {
        if (state.magicPathSelected.length > 0) {
            state.magicPathSelected = []
            store.state.charactersCreate.magicalAbilities = []

            if (state.maxPathMagicAbilities === 0) {
                state.maxPathMagicAbilities = state.maxPathMagicAbilities +1
            }
        }
        state.magicPathSelected = magicPath
    },
    setClearSelectedMagicPath (state) {
        state.magicPathSelected = []
        store.state.charactersCreate.magicalAbilities = []

        if (state.maxPathMagicAbilities === 0) {
            state.maxPathMagicAbilities = state.maxPathMagicAbilities +1
        }  
    },
    setAbility (state, ability) {      
        let abilityList = []

        if (ability.source.name == 'Herança') {
            abilityList = store.state.charactersCreate.heritageAbilities
        } else if (ability.source.name == 'Origem') {
            abilityList = store.state.charactersCreate.sourceAbilities
        } else if (ability.source.name == 'Geral') {
            abilityList = state.pathSelected[0].abilities
            store.state.charactersCreate.abilities = abilityList
        } else {
            abilityList = state.magicPathSelected[0].abilities
            store.state.charactersCreate.magicalAbilities = abilityList
        }

        abilityList.map(abilityItem => {
            if (abilityItem.id == ability.id) {

                ability.trained = !abilityItem.trained

                if (ability.trained) {
                    if (ability.source.name == 'Herança') {
                        state.maxHeritageAbilities = state.maxHeritageAbilities -1 
                    } else if (ability.source.name == 'Origem') {
                        state.maxSourceAbilities = state.maxSourceAbilities -1
                    } else if (ability.source.name == 'Geral') {
                        state.maxPathAbilities = state.maxPathAbilities -1
                    } else {
                        state.maxPathMagicAbilities = state.maxPathMagicAbilities -1
                    }

                    if (ability.sorcery) {
                        store.state.charactersCreate.sorcery = true
                    }
                } else {
                    if (ability.source.name == 'Herança') {
                        state.maxHeritageAbilities = state.maxHeritageAbilities +1 
                    } else if (ability.source.name == 'Origem') {
                        state.maxSourceAbilities = state.maxSourceAbilities +1
                    } else if (ability.source.name == 'Geral') {
                        state.maxPathAbilities = state.maxPathAbilities +1
                    } else {
                        state.maxPathMagicAbilities = state.maxPathMagicAbilities +1
                    }
                    
                    if (ability.sorcery) {
                        store.state.charactersCreate.sorcery = false
                    }
                }
            }
            return abilityItem
        })     
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}