<template>
    <div class="spells">
        <Divider v-if="maxSpells > 0" :step=7 title="Escolha Sortilégios" :button="true" label="Aprender novos Sortilégios" to="/create-character/spells" />
        <Divider v-else :step=7 title="Escolha Sortilégios" />

        <div v-if="maxSpells > 0" class="max-spells">
            <p>Aprenda {{ maxSpells }} sortilégio(s)</p>
        </div>

        <h2 class="spells-subtitle">
            <span class="material-icons">grid_view</span>
            Encantos
        </h2>

        <div class="enchant-row">
            <p class="enchant-info" v-if="maxEnchants > 0">Escolha <span>{{ maxEnchants }}</span> encanto:</p>

            <div v-if="characterEnchants.length > 0" class="enchant-grid">
                <div v-for="enchant in characterEnchants" :key="enchant.id">
                    <div class="enchant-item">
                        <p>
                            <span class="material-icons">auto_fix_high</span>
                            {{ enchant.name }}
                        </p>

                        <span @click="getEnchantInfo(enchant.id)" title="Ver detalhes" class="material-icons">info</span>

                        <span @click="deleteEnchant(enchant)" title="Esquercer encanto" class="material-icons delete">cancel</span>
                    </div>
                </div>
            </div>

            <div v-else class="enchant-grid">
                <div v-for="enchant in enchants" :key="enchant.id">
                    <div class="enchant-item">
                        <p @click="chooseEnchant(enchant)">
                            <span class="material-icons">auto_fix_high</span>
                            {{ enchant.name }}
                        </p>

                        <p @click="getEnchantInfo(enchant.id)">
                            <span class="material-icons">info</span>
                        </p>
                    </div>
                </div>
            </div> 
        </div>

        <h2 class="spells-subtitle">
            <span class="material-icons">grid_view</span>
            Grimório
        </h2>

        <div v-if="spells.length > 0" class="spell-list">
            <div class="spell-list-header">
                <p>Nome</p>
                <p>Eco</p>
                <p>Execução</p>
                <p>Efeito</p>
                <p>Alcance</p>
                <p>Duração</p>
                <p>Resistência</p>
                <p>Ações</p>
            </div>

            <div v-for="spell in spells" :key="spell.id" class="spell-list-item">
                <p>{{ spell.name }}</p>
                <p>{{ spell.grade }}</p>
                <p><Activation :value="spell.activation" /></p>
                <p>{{ spell.effect }}</p>
                <p>{{ spell.range }}</p>
                <p>{{ spell.duration }}</p>

                <p v-if="spell.resistance">{{ spell.resistance }}</p>
                <p v-else> - </p>
                
                <p class="spell-list-item-details">
                    <span @click="getSpellInfo(spell.id)" class="material-icons" title="Ver detalhes">info</span>
                    <span @click="deleteSpell(spell)" class="material-icons delete" titte="Remover sortilégio" >cancel</span>
                </p>
            </div>
        </div>

        <div v-else class="not-spells">
            <p>Você ainda não aprendeu nenhum Sortilégio.</p>
        </div>
    </div>
</template>

<script>
    // Import components
    import Divider from '@/components/template/Divider.vue'
    import Activation from '@/components/characters/Activation.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'Spells',
        components: {
            Divider, Activation 
        },
        created() {
            this.$store.dispatch('spells/getEnchants')
        },
        computed: {
            ...mapState({
                spells: state => state.charactersCreate.spells,
                characterEnchants: state => state.charactersCreate.enchants,
                maxSpells: state => state.charactersCreate.maxSpells,
                maxEnchants: state => state.charactersCreate.maxEnchants,
                spellInfo: state => state.spells.spellInfo,
                enchants: state => state.spells.enchants,
                enchantInfo: state => state.spells.enchantInfo                
            })
        },
        methods: {
            getSpellInfo(id) {
                this.$store.dispatch('spells/getSpellInfo', id)             
            },
            getEnchantInfo(id) {
                this.$store.dispatch('spells/getEnchantInfo', id)            
            },
            chooseEnchant(enchant) {
                this.$store.dispatch('spells/chooseEnchant', enchant)
            },
            deleteEnchant(enchant) {
                if (confirm('Deseja esquecer o encanto ' + enchant.name + '?')) {
                    this.$store.dispatch('spells/deleteEnchant')
                }
            },
            deleteSpell(spell) {
                if (confirm('Deseja esquecer o sortilégio ' + spell.name + '?')) {
                    this.$store.dispatch('spells/deleteSpell',  spell.id)
                }
            }
            
        }
    }
</script>

