import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    motivationList: [],
    motivationInfo: [],
    motivationSelected: false,
    
})

const getters = {}

const actions = {
    async getMotivationList({ commit }) {
        const motivations = await api.get('/motivations/?fields=id,name')
        commit('setMotivationList', motivations.data.results)
    },
    async getMotivationInfo ({ commit }, id) {
        const motivationInfo = await api.get('/motivations/?id=' + id)
        commit('setMotivationInfo', motivationInfo.data.results)
    },
    getMotivationState ({ commit }, motivationState) {
        commit('setMotivationSelected', motivationState)
    },
    getCharacterMotivation({ commit }, motivation ) {
        commit('setCharacterMotivation', motivation)
    },
}

const mutations = {
    setMotivationList (state, motivations) {
        state.motivationList = motivations
    },
    setMotivationInfo (state, motivationInfo) {
        state.motivationInfo = motivationInfo
    },
    setMotivationSelected (state, motivationState) {
        state.motivationSelected = motivationState
    },
    setCharacterMotivation (state, motivation) {
        store.state.charactersCreate.motivation = motivation
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}