<template>
    <div>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>

        <Section name="Crie seu Personagem" />

        <Form @submit="onSubmit">
            <Divider :step=1 title="Escolha Características Básicas" />

            <div class="level">
                <p>Nível {{ level }}</p>
            </div>

            <div class="characters-create-row">
                <div class="form-item">
                    <label>Nome</label>
                    <Field name="name" type="text" :rules="{ required: true }" label="Nome" />
                    <ErrorMessage name="name" />
                </div>

                <div class="form-item">
                    <label>Sobrenome</label>
                    <Field name="last_name" type="text" label="Sobrenome" />
                </div>

                <div class="form-item">
                    <label>Sexo</label>
                    <Field name="gender" as="select" :rules="{ required: true }" label="Sexo">
                        <option v-for="gender in genders" :key="gender.id" :value="gender.id">{{ gender.name }}</option>
                    </Field>
                    <ErrorMessage name="gender" />
                </div>
            </div>

            <div class="characters-create-row">
                <div class="form-item">
                    <label>Idade</label>
                    <Field name="age" type="number" label="idade" />
                </div>

                <div class="form-item">
                    <label>Altura</label>
                    <Field name="height" type="number" label="Altura" />
                </div>

                <div class="form-item">
                    <label>Peso</label>
                    <Field name="weight" type="number" label="Peso" />
                </div> 
            </div>

            <Divider :step=2 title="Escolha Herança, Origem e Motivação" />

            <div class="characters-create-row">
                <div class="characters-create-choice-link">
                    <ChoiceLink title="Escolha sua Herança" to="/create-character/heritages" :value="heritage" />
                    
                    <div class="form-item">
                        <Field name="heritage" type="hidden" :rules="{ required: true }" label="Herança" v-model="heritage" />
                        <ErrorMessage name="heritage" />
                    </div>
                </div>    

                <div class="characters-create-choice-link">
                    <ChoiceLink title="Escolha sua Origem" to="/create-character/sources" :value="source" :impulse="true" />
                    
                    <div class="form-item">
                        <Field name="source" type="hidden" :rules="{ required: true }" label="Origem" v-model="source" />
                        <ErrorMessage name="source" />
                    </div>
                </div>  

                <div class="characters-create-choice-link">
                    <ChoiceLink title="Escolha sua Motivação" to="/create-character/motivations" :value="motivation" :impulse="true" />  
                    
                    <div class="form-item">
                        <Field name="motivation" type="hidden" :rules="{ required: true }" label="Motivação" v-model="motivation" />
                        <ErrorMessage name="motivation" />
                    </div>
                </div>  
            </div>

            <Attributes />

            <div class="form-item">
                <Field name="attributePoints" type="hidden" :rules="{ is: 0 }" label="Atributos" v-model="attributePoints" />
                <ErrorMessage name="attributePoints" />

                <Field name="hasAffinityPoints" type="hidden" :rules="{ is: 0 }" label="Afinidades" v-model="hasAffinityPoints" />
                <ErrorMessage name="hasAffinityPoints" />
            </div>

            <Skills />

            <div class="form-item">
                <Field name="hasSkillPoints" type="hidden" :rules="{ is: 0 }" label="Perícias" v-model="hasSkillPoints" />
                <ErrorMessage name="hasSkillPoints" />
            </div>

            <Abilities />

            <div class="form-item">
                <Field name="heritageAbilities" type="hidden" :rules="{ is_not: 0 }" label="Habilidade de Herança" v-model="heritageAbilities" />
                <ErrorMessage name="heritageAbilities" />

                <Field name="sourceAbilities" type="hidden" :rules="{ is_not: 0 }" label="Habilidade de Origem" v-model="sourceAbilities" />
                <ErrorMessage name="sourceAbilities" />

                <Field name="abilities" type="hidden" :rules="{ is_not: 0 }" label="Habilidade Geral" v-model="abilities" />
                <ErrorMessage name="abilities" />

                <Field name="magicalAbilities" type="hidden" :rules="{ is_not: 0 }" label="Habilidade Mágica" v-model="magicalAbilities" />
                <ErrorMessage name="magicalAbilities" />
            </div>

            <Equipments />

            <div class="form-item">
                <Field name="maxInitialArmor" type="hidden" :rules="{ is_not: 1 }" label="Armadura Inicial" v-model="maxInitialArmor" />
                <ErrorMessage name="maxInitialArmor" />
            </div>

            <Spells v-if="sorcery" />

            <div v-if="sorcery" class="form-item">
                <Field name="maxEnchants" type="hidden" :rules="{ is: 0 }" label="Encantos" v-model="maxEnchants" />
                <ErrorMessage name="maxEnchants" />

                <Field name="maxSpells" type="hidden" :rules="{ is: 0 }" label="Sortilégios" v-model="maxSpells" />
                <ErrorMessage name="maxSpells" />
            </div>
     
            <button>Salvar Personagem</button>
        </Form>
    </div>
</template>

<script>
    // Import components
    import Section from '@/components/template/Section.vue'
    import Divider from '@/components/template/Divider.vue'
    import ChoiceLink from '@/components/characters/ChoiceLink.vue'
    import Attributes from '@/components/characters/Attributes.vue'
    import Skills from '@/components/characters/Skills.vue'
    import Abilities from '@/components/characters/Abilities.vue'
    import Equipments from '@/components/characters/Equipments.vue'
    import Spells from '@/components/characters/Spells.vue'
    
    
    // Import packages
    import { mapState } from 'vuex'
    import { Field, Form, ErrorMessage } from 'vee-validate';
    // import api from "@/utils/axios";
    
    export default {
        name: 'CreateCharacters',
        components: {
            Section, Divider, ChoiceLink, Attributes, Skills, Abilities, Equipments, Spells, Field, Form, ErrorMessage
        },
        created() {
            if (this.genders.length === 0) {
                this.$store.dispatch('charactersCreate/getGenders')
            }
        },
        computed: {
            ...mapState({
                level: state => state.charactersCreate.level,
                genders: state => state.charactersCreate.lists.genders,
                heritage: state => state.charactersCreate.heritage,
                source: state => state.charactersCreate.source,
                motivation: state => state.charactersCreate.motivation,
                attributePoints: state => state.charactersCreate.attributePoints.length,
                affinityPoints: state => state.charactersCreate.affinityPoints,
                mind: state => state.charactersCreate.mind,
                cunning: state => state.charactersCreate.cunning,
                spentSkillPoints: state => state.charactersCreate.spentSkillPoints,
                heritageAbilities: state => state.charactersCreate.heritageAbilities.length,
                sourceAbilities: state => state.charactersCreate.sourceAbilities.length,
                abilities: state => state.charactersCreate.abilities.length,
                magicalAbilities: state => state.charactersCreate.magicalAbilities.length,
                maxInitialArmor: state => state.equipments.maxInitialArmor,
                sorcery: state => state.charactersCreate.sorcery,
                maxEnchants: state => state.charactersCreate.maxEnchants,
                maxSpells: state => state.charactersCreate.maxSpells
            }),
            hasAffinityPoints() {
                if (this.affinityPoints.body > 0) {
                    return 1
                } else if (this.affinityPoints.mind > 0) {
                    return 1
                } else if (this.affinityPoints.ether > 0) {
                    return 1
                } else {
                    return 0
                }
            },
            hasSkillPoints() {
                const skillPoints = (this.mind * 5) + this.cunning

                if (skillPoints === this.spentSkillPoints) {
                    return 0
                } else {
                    return 1
                }
            }
        },
        methods: {
            back() {
                if (confirm('Deseja sair da criação de personagens? Os dados serão perdidos!')) {
                    this.$store.dispatch('charactersCreate/clearNewCharacter')
                    this.$router.push("/characters")
                }                
            },
            onSubmit(values,actions) {
                this.$store.dispatch('charactersCreate/createCharacter', values)
                this.$store.dispatch('charactersCreate/clearNewCharacter')
                actions.resetForm()
                this.$router.push("/characters")
            }
        }
    }
</script>

<style scoped>
    .level {
        display: flex;
        justify-content: end;
    }
    .level p {
        border: none;
        border-bottom: 2px solid #d5d5d5;
        border-left: 2px solid #d5d5d5;
        border-right: 2px solid #d5d5d5;
        font-size: 20px;
        margin-right: 32px;
        margin-top: -64px;
        padding: 16px;
        text-align: center;
    }   
    .characters-create-row {
        display: flex;
    }
    .characters-create-row .form-item {
        margin-right: 32px;
    }
    .characters-create-choice-link {
        width: 100%;
    }
    .characters-create-choice-link .form-item {
        margin: 16px 0 0 16px;
    }
</style>