import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    heritagesList: [],  
    heritageInfo: [],
    heritageSelected: false
})

const getters = {}

const actions = {
    async getHeritagesList ({ commit }) {
        const heritages = await api.get('/heritages/?fields=id,name')
        commit('setHeritagesList', heritages.data.results)
    },
    async getHeritageInfo ({ commit }, id) {
        const heritageInfo = await api.get('/heritages/?id=' + id)
        commit('setHeritageInfo', heritageInfo.data.results)
    },
    getHeritageState ({ commit }, heritageState) {
        commit('setHeritageSelected', heritageState)
    },
    getCharacterHeritage ({ commit }, heritage ) {
        commit('setCharacterHeritage', heritage)
    }
}

const mutations = {
    setHeritagesList (state, heritages) {
        state.heritagesList = heritages
    },
    setHeritageInfo (state, heritageInfo) {
        state.heritageInfo = heritageInfo
    },
    setHeritageSelected (state, heritageState) {
        state.heritageSelected = heritageState
    },
    setCharacterHeritage (state, heritage) {
        store.state.charactersCreate.heritage = heritage
        store.state.charactersCreate.size = heritage.size
        store.state.charactersCreate.heritageAbilities = []

        for (let ability in heritage.path.abilities) {
            let trained = false

            if (heritage.path.abilities[ability].initial) {
                trained = true
            }

            const trainedAbility = {
                activation: heritage.path.abilities[ability].activation,
                cost: heritage.path.abilities[ability].cost,
                description: heritage.path.abilities[ability].description,
                id: heritage.path.abilities[ability].id,
                initial: heritage.path.abilities[ability].initial,
                name: heritage.path.abilities[ability].name,
                requirement: heritage.path.abilities[ability].requirement,
                resource: heritage.path.abilities[ability].resource,
                traits: heritage.path.abilities[ability].traits,
                trained: trained,
                path: heritage.path.id,
                source: heritage.path.abilities[ability].source,
            }

            store.state.charactersCreate.heritageAbilities.push(trainedAbility)
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}