<template>
    <div class="login">
        <div>
            <Section name="Login" />
            
            <Form @submit="onSubmit">
                <div class="form-item">
                    <label>Nome de Usuário</label>
                    <Field name="username" type="text" :rules="{ required: true }" label="Nome de Usuário" class="login-input" />
                    <ErrorMessage name="username" />
                </div>
                
                <div class="form-item">
                    <label>Senha</label>
                    <Field name="password" type="password" :rules="{ required: true }" label="Senha" class="login-input" />
                    <ErrorMessage name="password" />
                </div>

                <button class="login-button">Logar</button>
            </Form>

            <div v-if="unauthorized">
                <p class="unauthorized">Usuário ou senha inválidos.</p>
            </div>
        </div>
    </div>
</template>

<script>
    // Import Components
    import Section from '@/components/template/Section.vue'

    // Import packages
    import { Field, Form, ErrorMessage } from 'vee-validate';

    export default {
        name: 'Login',
        components: {
            Section, Field, Form, ErrorMessage 
        },
        computed: {
            unauthorized() {
                return this.$store.state.login.unauthorized
            }
        },
        methods: {
            onSubmit(values, actions) {
                this.$store.dispatch('login/authenticate', values)
                actions.resetForm()
            }
        }
    }
</script>

<style scoped>
    .login {
        text-align: center;
    }
    .login-input {
        margin: 0 auto;
    }
    .unauthorized {
        color: #ef4444;
        display: block;
        font-size: 20px;
        margin-top: 8px;
    }
</style>