import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    skillList: [],
    skillListInfo: [],
    skillListSelected: false
})

const getters = {

}

const actions = {
    async getSkillInfo ({ commit }, id) {
        let skillId = 0

        if (id.length > 1) {
            skillId = id.slice(0, 1)
        } else {
            skillId = id
        }

        const skill = await api.get('/skills/?id=' + skillId)
        commit('setSkillInfo', skill.data.results)
    },
    spentSkillPoints({ commit }) {
        commit('setSpentSkillPoints')
    },
    addSkillPoints({ commit }) {
        commit('setAddSkillPoints')
    },
    async getSkillList ({ commit }) {
        const skills = await api.get('/skills/?fields=id,name,attribute_label,affinity,affinity_label,armor_penality,specialization')
        commit('setSkillList', skills.data.results)
    },
    async getSkillListInfo ({ commit }, id) {
        const skillInfo = await api.get('/skills/?id=' + id)
        commit('setSkillListInfo', skillInfo.data.results)
    },
    getSkillState ({ commit }, skillState) {
        commit('setSkillListSelected', skillState)
    },
    saveSkill({ commit }, values) {
        commit('setSkills', values)
    },
    updateSkills ({ commit }, values) {
        commit('setUpdateSkills', values)
    }
}

const mutations = {
    setSkillInfo (state, skill) {
        if (store.state.infoScreen.id === skill[0].id) {
            store.state.infoScreen.id = 0
            store.state.infoScreen.info = []
            store.state.infoScreen.show = false
            store.state.infoScreen.type = ''
        } else {
            store.state.infoScreen.id = skill[0].id
            store.state.infoScreen.info = skill[0]
            store.state.infoScreen.show = true
            store.state.infoScreen.type = 'skill'
        }
    },
    setSpentSkillPoints () {
        store.state.charactersCreate.spentSkillPoints = store.state.charactersCreate.spentSkillPoints +1
    },
    setAddSkillPoints () {
        store.state.charactersCreate.spentSkillPoints = store.state.charactersCreate.spentSkillPoints -1
    },
    setSkillList (state, skills) {
        const skillList = skills.map(skillItem => {
            skillItem.specializationsLearned = 0
            return skillItem
        })
 
        state.skillList = skillList
    },
    setSkillListInfo (state, skillInfo) {
        state.skillListInfo = skillInfo
    },
    setSkillListSelected (state, skillState) {
        state.skillListSelected = skillState
    },
    setSkills (state, values) {
        const specializationList = values.skill.specialization
        
        let setSpecialization = []

        for (let specialization in specializationList) {
            const specializationId = specializationList[specialization].id

            if (specializationId == values.specializationSelected) {
                setSpecialization = specializationList[specialization]
                store.state.charactersCreate.specializations.push(setSpecialization)
            }
        }

        let skillId = ''

        if (setSpecialization.length === 0) {
            skillId = values.skill.id
        } else {
            skillId = values.skill.id + '-' + setSpecialization.id
        }

        const newSkill = {
            id: skillId,
            name: values.skill.name,
            attribute: values.skill.attribute,
            attribute_label: values.skill.attribute_label,
            affinity: values.skill.affinity,
            affinity_label: values.skill.affinity_label,
            description: values.skill.description,
            specialization: setSpecialization,
            actions: values.skill.actions,
            traits: values.skill.traits,
            armor_penality: values.skill.armor_penality,
            training_level: 1,
            source_skill: false    
        }
        store.state.charactersCreate.skills.push(newSkill)

        store.state.charactersCreate.spentSkillPoints = store.state.charactersCreate.spentSkillPoints +1
    },
    setUpdateSkills(state, values) {      
        const temporaryList = state.skillList

        state.skillList = []

        for (let item in temporaryList) {
            if (temporaryList[item].id === values.skill.id ) {
                if (values.specializationSelected) {                    
                    if (temporaryList[item].specializationsLearned != temporaryList[item].specialization.length) {
                        
                        const updateSpecializationsLearned = temporaryList[item].specializationsLearned +1

                        if (updateSpecializationsLearned != temporaryList[item].specialization.length) {
                            
                            const newObject = {
                                affinity: temporaryList[item].affinity,
                                affinity_label: temporaryList[item].affinity_label,
                                armor_penality: temporaryList[item].armor_penality,
                                attribute_label: temporaryList[item].attribute_label,
                                id: temporaryList[item].id,
                                name: temporaryList[item].name,
                                specialization: temporaryList[item].specialization,
                                specializationsLearned: updateSpecializationsLearned,
                                trained: false     
                            }

                            state.skillList.push(newObject)
                        }
                    }                 
                }
            } else {
                state.skillList.push(temporaryList[item])
            }
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}