// Imports vue
import { createStore } from 'vuex'
import createPersistedState from "vuex-persistedstate"

//  Modules System
import infoScreen from './modules/system/infoScreen'
import login from './modules/system/login'
import notifications from './modules/system/notifications'

// Modules Screens
import heritages from './modules/screens/heritages'
import motivations from './modules/screens/motivations'
import sources from './modules/screens/sources'

// Modules Character
import abilities from './modules/characters/abilities'
import characters from './modules/characters/characters'
import charactersCreate from './modules/characters/charactersCreate'
import equipments from './modules/characters/equipments'
import skills from './modules/characters/skills'
import spells from './modules/characters/spells'

const persistedState = createPersistedState({
    paths: ['login']
})

export default createStore({
    modules: {
        infoScreen,
        login,
        notifications,
        heritages,
        motivations, 
        sources,
        abilities,
        characters,
        charactersCreate,
        equipments,
        skills,
        spells
    },
    plugins: [persistedState]
})