<template>
    <Divider :step=3 title="Escolha Atributos e Afinidades" />
    
    <div class="attribute-row">        
        <div class="attribute-area">
            <div class="attribute">
                <p>Corpo</p>

                <div v-if="selectedBody === 0">
                    <select v-model="selectedBody" name="body" id="body" class="attribute-select">
                        <option v-for="attribute in attributePoints" :key="attribute" :value="attribute">{{ attribute }}</option>
                    </select>
                </div>

                <div v-else class="attribute-selected">
                    <p>{{ body }}</p>
                    <span class="material-icons" @click="releaseAttributePoints('body', selectedBody)" title="Liberar Atributo">cancel</span>
                </div>
            </div>
            
            <div v-show="selectedBody > 0" class="affinities">
                <Affinity name="Agilidade" attribute="body" affinity="agility" :affinityLevel="agility" :maxAffinityLevel="maxBodyAffinityLevel" :affinityPoints="bodyAffinityPoints" />
                <Affinity name="Destreza" attribute="body" affinity="dexterity" :affinityLevel="dexterity" :maxAffinityLevel="maxBodyAffinityLevel" :affinityPoints="bodyAffinityPoints" />
                <Affinity name="Força" attribute="body" affinity="strength" :affinityLevel="strength" :maxAffinityLevel="maxBodyAffinityLevel" :affinityPoints="bodyAffinityPoints" />
                <Affinity name="Vigor" attribute="body" affinity="stamina" :affinityLevel="stamina" :maxAffinityLevel="maxBodyAffinityLevel" :affinityPoints="bodyAffinityPoints" />
            </div>            
        </div>

        <div class="attribute-area">
            <div class="attribute">
                <p>Mente</p>

                <div v-if="selectedMind === 0">
                    <select v-model="selectedMind" name="mind" id="mind" class="attribute-select">
                        <option v-for="attribute in attributePoints" :key="attribute" :value="attribute">{{ attribute }}</option>
                    </select>
                </div>

                <div v-else class="attribute-selected">
                    <p>{{ mind }}</p>
                    <span class="material-icons" @click="releaseAttributePoints('mind', selectedMind)" title="Liberar Atributo">cancel</span>
                </div>
            </div>
            
            <div v-show="selectedMind > 0" class="affinities">
                <Affinity name="Astúcia" attribute="mind" affinity="cunning" :affinityLevel="cunning" :maxAffinityLevel="maxMindAffinityLevel" :affinityPoints="mindAffinityPoints" />
                <Affinity name="Influência" attribute="mind" affinity="influence" :affinityLevel="influence" :maxAffinityLevel="maxMindAffinityLevel" :affinityPoints="mindAffinityPoints" />
                <Affinity name="Percepção" attribute="mind" affinity="perception" :affinityLevel="perception" :maxAffinityLevel="maxMindAffinityLevel" :affinityPoints="mindAffinityPoints" />
                <Affinity name="Vontade" attribute="mind" affinity="will" :affinityLevel="will" :maxAffinityLevel="maxMindAffinityLevel" :affinityPoints="mindAffinityPoints" />
            </div>            
        </div>

        <div class="attribute-area">
            <div class="attribute">
                <p>Ether</p>

                <div v-if="selectedEther === 0">
                    <select v-model="selectedEther" name="ether" id="ether" class="attribute-select">
                        <option v-for="attribute in attributePoints" :key="attribute" :value="attribute">{{ attribute }}</option>
                    </select>
                </div>
                
                <div v-else class="attribute-selected">
                    <p>{{ ether }}</p>
                    <span class="material-icons" @click="releaseAttributePoints('ether', selectedEther)" title="Liberar Atributo">cancel</span>
                </div>
            </div>
            
            <div v-show="selectedEther > 0" class="affinities">
                <Affinity name="Água" attribute="ether" affinity="water" :affinityLevel="water" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Chama" attribute="ether" affinity="fire" :affinityLevel="fire" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Brisa" attribute="ether" affinity="air" :affinityLevel="air" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Terra" attribute="ether" affinity="earth" :affinityLevel="earth" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Lua" attribute="ether" affinity="moon" :affinityLevel="moon" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Natureza" attribute="ether" affinity="nature" :affinityLevel="nature" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Luz" attribute="ether" affinity="light" :affinityLevel="light" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
                <Affinity name="Escuridão" attribute="ether" affinity="darkness" :affinityLevel="darkness" :maxAffinityLevel="maxEtherAffinityLevel" :affinityPoints="etherAffinityPoints" />
            </div>           
        </div>
    </div>
</template>

<script>
    // Import components
    import Divider from '@/components/template/Divider.vue'
    import Affinity from '@/components/characters/Affinity.vue'

    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'Attribute',
        components: {
            Divider, Affinity
        },
        data() {
            return {
                selectedBody: 0,
                selectedMind: 0,
                selectedEther: 0,
            }
        },
        computed: {
            ...mapState({
                // Attributes
                body: state => state.charactersCreate.body,
                mind: state => state.charactersCreate.mind,
                ether: state => state.charactersCreate.ether,
                attributePoints: state => state.charactersCreate.attributePoints,
                
                // Affinities
                agility: state => state.charactersCreate.agility,
                dexterity: state => state.charactersCreate.dexterity,
                strength: state => state.charactersCreate.strength,
                stamina: state => state.charactersCreate.stamina,
                cunning: state => state.charactersCreate.cunning,
                influence: state => state.charactersCreate.influence,
                perception: state => state.charactersCreate.perception,
                will: state => state.charactersCreate.will,
                water: state => state.charactersCreate.water,
                fire: state => state.charactersCreate.fire,
                air: state => state.charactersCreate.air,
                earth: state => state.charactersCreate.earth,
                moon: state => state.charactersCreate.moon,
                nature: state => state.charactersCreate.nature,
                light: state => state.charactersCreate.light,
                darkness: state => state.charactersCreate.darkness,

                // Limits
                maxBodyAffinityLevel: state => state.charactersCreate.maxAffinityLevel.body,
                maxMindAffinityLevel: state => state.charactersCreate.maxAffinityLevel.mind,
                maxEtherAffinityLevel: state => state.charactersCreate.maxAffinityLevel.ether,
                bodyAffinityPoints: state => state.charactersCreate.affinityPoints.body,
                mindAffinityPoints: state => state.charactersCreate.affinityPoints.mind,
                etherAffinityPoints: state => state.charactersCreate.affinityPoints.ether
            })
        },
        methods: {
            releaseAttributePoints(attribute, value) {
                const updateAttribute = {
                        attribute: attribute,
                        value: value
                }
                                
                this.$store.dispatch('charactersCreate/releaseAttributePoints', updateAttribute)

                if (attribute === 'body') {
                    this.selectedBody = 0
                } else if (attribute === 'mind') {
                    this.selectedMind = 0
                } else if (attribute === 'ether') {
                    this.selectedEther = 0
                }   
            }
        },
        watch: {
            selectedBody() {

                if (this.selectedBody > 0) {
                    const updateAttribute = {
                        attribute: 'body',
                        value: this.selectedBody
                    }
                    this.$store.dispatch('charactersCreate/updateAttributePoints', updateAttribute)
                }
            },
            selectedMind() {
                if (this.selectedMind > 0) {
                    const updateAttribute = {
                        attribute: 'mind',
                        value: this.selectedMind
                    }
                    this.$store.dispatch('charactersCreate/updateAttributePoints', updateAttribute)
                }
            },
            selectedEther() {
                if (this.selectedEther > 0) {
                    const updateAttribute = {
                        attribute: 'ether',
                        value: this.selectedEther
                    }
                    this.$store.dispatch('charactersCreate/updateAttributePoints', updateAttribute)
                }
            }
        }
    }
</script>

