<template>
    <div>
        <div v-if="!$route.params.id">
            <div v-if="loadingCharacterList" class="loading">
                <img src="@/assets/loading.gif" alt="Carregando">
            </div>
            
            <div v-else>
                <Section name="Personagens" />

                <Divider icon="groups" title="Meus Personagens" :button="true" label="Criar Personagem" to="/create-character" />
                
                <div v-if="characterList.length > 0" class="characters-row">
                    <div class="characters" v-for="character in characterList" :key="character.id">
                        <router-link :to="'/characters/' + character.id" tag="a">                    
                            <div class="characters-portrait">
                                <img src="https://tconvidei.com.br/wp-content/uploads/2021/06/128-1280406_view-user-icon-png-user-circle-icon-png-1.jpg" alt="portrait">
                                <p>{{ character.name }} {{ character.last_name }}</p>
                            </div>

                            <div class="characters-level">
                                <p>Nível</p>
                                <span>{{ character.level }}</span>
                            </div>

                            <div class="characters-experience">
                                <p>Experiência</p>
                                <progress id="experience" :value="character.experience" max="100" />
                            </div>
                            
                            <div class="text-align-center">
                                <p>Criado em {{ character.created }}</p>
                            </div>

                            <div class="characters-delete" @click.prevent="deleteCharacter(character.id)">
                                <span class="material-icons">close</span>
                                <p>Excluir</p>
                            </div>
                        </router-link>
                    </div>
                </div>

                <p class="characters-empty" v-else>Você ainda não tem nenhum personagem criado.</p>
            </div>
            
        </div>

        <router-view></router-view>
    </div>
</template>

<script>
    // Import components
    import Divider from '@/components/template/Divider.vue'
    import Section from '@/components/template/Section.vue'

    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'CharactersList',
        components: {
            Divider, Section
        },
        created() {
            this.$store.dispatch('characters/getCharacterList', this.userId)
        },
        computed: {
            ...mapState({
                userId: state => state.login.userId,
                loadingCharacterList: state => state.characters.loadingCharacterList,
                characterList: state => state.characters.characterList
            })
        },
        methods: {
            deleteCharacter(id) {
                if (confirm('Deseja excluir esse personagem?')) {
                    this.$store.dispatch('characters/deleteCharacter', id)
                }   
            }
        }
    }
</script>

