import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    level: 1,
    name: '',
    last_name: '',
    gender: '',
    age: '',
    height: '',
    weight: '',
    size: '',
    heritage: [],
    heritageAbilities: [],
    source: [],
    sourceAbilities: [],
    motivation: [],
    attributePoints: [1, 2, 3],
    affinityPoints: {
        body: 0,
        mind: 0,
        ether: 0
    },
    maxAffinityLevel: {
        body: 1,
        mind: 1,
        ether: 1     
    },
    body: 1,
    agility: 0,
    dexterity: 0,
    strength: 0,
    stamina: 0,
    mind: 1,
    cunning: 0,
    influence: 0,
    perception: 0,
    will: 0,
    ether: 1,
    water: 0,
    fire: 0,
    air: 0,
    earth: 0,
    moon: 0,
    nature: 0,
    light: 0,
    darkness: 0,
    skills: [],
    skillDetail: [],
    spentSkillPoints: 0,
    specializations: [],
    abilities: [],
    magicalAbilities: [],
    equipments: {
        armors: [],
        shields: [],
        weapons: [],
        items: []
    },
    armorPenality: 0,
    sorcery: false,
    spells: [],
    enchants: [],
    maxSpells: 3,
    maxEnchants: 1,
    geo: 30,
    lists: {
        genders: []
    }
})

const getters = {}

const actions = {
    async getGenders ({ commit }) {
        const genders = await api.get('/genders/')
        commit('setGenders', genders.data.results)
    },
    releaseAttributePoints ({ commit }, updateAttribute) {
        commit('setReleaseAttributePoints', updateAttribute)
    },
    updateAttributePoints ({ commit }, updateAttribute) {
        commit('setUpdateAttributePoints', updateAttribute)
    },
    selectAffinityLevel ({ commit }, affinityChoice ) {
        commit('setSelectAffinityLevel', affinityChoice )
    },
    createCharacter ({ commit }, values) {
        commit('setCreateCharacter', values)
    },
    clearNewCharacter ({ commit }) {
        commit('setClearNewCharacter')
    }
}

const mutations = {
    setGenders (state, genders) {
        state.lists.genders = genders
    },
    setReleaseAttributePoints (state, updateAttribute) {
        state.attributePoints.push(updateAttribute.value)
        
        state[updateAttribute.attribute] = 1
        state.affinityPoints[updateAttribute.attribute] = 0

        if (updateAttribute.attribute === 'body') {
            state.agility = 0
            state.dexterity = 0
            state.strength = 0
            state.stamina = 0
        }

        if (updateAttribute.attribute === 'mind') {
            state.cunning = 0
            state.influence = 0
            state.perception = 0
            state.will = 0
        }

        if (updateAttribute.attribute === 'ether') {
            state.water = 0
            state.fire = 0
            state.air = 0
            state.earth = 0
            state.moon = 0
            state.nature = 0
            state.light = 0
            state.darkness = 0
        }

        state.attributePoints.sort(function(a,b) {
            return a - b;
        });

        if (state.skills.length > 0) {
            state.skills.map(skillItem => {
                if(skillItem.training_level > 1) {
                    const refundSkillPoint = skillItem.training_level -1
                    state.spentSkillPoints = state.spentSkillPoints - refundSkillPoint
                    skillItem.training_level = 1
                }
            })
        }
    },
    setUpdateAttributePoints (state, updateAttribute) {
        state.attributePoints.splice(state.attributePoints.findIndex(index => index === updateAttribute.value), 1)
        state[updateAttribute.attribute] = updateAttribute.value
        state.maxAffinityLevel[updateAttribute.attribute] = updateAttribute.value
        state.affinityPoints[updateAttribute.attribute] = updateAttribute.value
    },
    setSelectAffinityLevel (state, affinityChoice) {
        if (affinityChoice.level == state[affinityChoice.affinity]) {
            state[affinityChoice.affinity] = state[affinityChoice.affinity] -1
            state.affinityPoints[affinityChoice.attribute] = state.affinityPoints[affinityChoice.attribute] +1

        } else {
            state.affinityPoints[affinityChoice.attribute] = state.affinityPoints[affinityChoice.attribute] - (affinityChoice.level - state[affinityChoice.affinity])
            state[affinityChoice.affinity] = affinityChoice.level
        }

        if (state.skills.length > 0) {
            state.skills.map(skillItem => {
                if(skillItem.training_level > 1) {
                    const refundSkillPoint = skillItem.training_level -1
                    state.spentSkillPoints = state.spentSkillPoints - refundSkillPoint
                    skillItem.training_level = 1
                }
            })
        }
    },
    setCreateCharacter (state, values) {
        // # Generate Clean Ability List
        let abilityList = []

        // ## Pick Heritage Abilities
        for (let ability in state.heritageAbilities) {                        
            if (state.heritageAbilities[ability].initial === true || state.heritageAbilities[ability].trained === true ) {
                const newAbility = {
                    id: state.heritageAbilities[ability].id,
                    name: state.heritageAbilities[ability].name
                }
                abilityList.push(newAbility)
            }
        }

        // ## Pick Source Abilities
        for (let ability in state.sourceAbilities) {                   
            if (state.sourceAbilities[ability].initial === true || state.sourceAbilities[ability].trained === true ) {
                const newAbility = {
                    id: state.sourceAbilities[ability].id,
                    name: state.sourceAbilities[ability].name
                }
                abilityList.push(newAbility)
            }
        }

        // ## Pick General Abilities
        for (let ability in state.abilities) {                   
            if (state.abilities[ability].initial === true || state.abilities[ability].trained === true ) {
                const newAbility = {
                    id: state.abilities[ability].id,
                    name: state.abilities[ability].name
                }
                abilityList.push(newAbility)
            }
        }

        // ## Pick Magical Abilities
        for (let ability in state.magicalAbilities) {                   
            if (state.magicalAbilities[ability].initial === true || state.magicalAbilities[ability].trained === true ) {
                const newAbility = {
                    id: state.magicalAbilities[ability].id,
                    name: state.magicalAbilities[ability].name
                }
                abilityList.push(newAbility)
            }
        }

        // Generate Clean Skill List

        let skillList = []

        for (let skill in state.skills) {
            const newSkill = {
                id: state.skills[skill].id,
                name: state.skills[skill].name,
                specialization: state.skills[skill].specialization,
                training_level: state.skills[skill].training_level
            }
            skillList.push(newSkill)
        }   

        // Generate Clean Spell List

        let spelllList = []

        for (let spell in state.spells) {
            const newSpell = {
                id: state.spells[spell].id,
                name: state.spells[spell].name,
                grade: state.spells[spell].grade,
                afinity: state.spells[spell].afinity
            }
            spelllList.push(newSpell)
        }  

        const newCharacter = {
            username: store.state.login.userId,
            name: values.name,
            last_name: values.last_name,
            gender: values.gender,
            age: values.age,
            height: values.height,
            weight: values.weight,
            heritage: values.heritage.id,
            source: values.source.id,
            motivation: values.motivation.id,
            body: state.body,
            agility: state.agility,
            dexterity: state.dexterity,
            strength: state.strength,
            stamina: state.stamina,
            mind: state.mind,
            cunning: state.cunning,
            influence: state.influence,
            perception: state.perception,
            will: state.will,
            ether: state.ether,
            water: state.water,
            fire: state.fire,
            air: state.air,
            earth: state.earth,
            moon: state.moon,
            nature: state.nature,
            light: state.light,
            darkness: state.darkness,
            skills: JSON.stringify(skillList),
            abilities: JSON.stringify(abilityList),
            equipments: JSON.stringify(state.equipments),
            spells: JSON.stringify(spelllList),
            enchants: JSON.stringify(state.enchants),
            geo: state.geo
        }

        api.post('/characters/', newCharacter).then(response => {
            if (response.status === 201 ) {
                store.state.notifications.message = 'Personagem ' + newCharacter.name + ' criado.'
                store.state.notifications.type = 'success'
                store.state.notifications.show = true
                store.state.notifications.persistant = false
            }
        })
    },
    setClearNewCharacter (state) {
        state.level = 1,
        state.name = '',
        state.last_name = '',
        state.gender = '',
        state.age = '',
        state.height = '',
        state.weight = '',
        state.size = '',
        state.heritage = [],
        state.heritageAbilities = [],
        state.source = [],
        state.sourceAbilities = [],
        state.motivation = [],
        state.attributePoints = [1, 2, 3],
        state.affinityPoints = {
            body: 0,
            mind: 0,
            ether: 0
        },
        state.maxAffinityLevel = {
            body: 1,
            mind: 1,
            ether: 1     
        },
        state.body = 1,
        state.agility = 0,
        state.dexterity = 0,
        state.strength = 0,
        state.stamina = 0,
        state.mind = 1,
        state.cunning = 0,
        state.influence = 0,
        state.perception = 0,
        state.will = 0,
        state.ether = 1,
        state.water = 0,
        state.fire = 0,
        state.air = 0,
        state.earth = 0,
        state.moon = 0,
        state.nature = 0,
        state.light = 0,
        state.darkness = 0,
        state.skills = [],
        state.skillDetail = [],
        state.spentSkillPoints = 0,
        state.specializations = [],
        state.abilities = [],
        state.magicalAbilities = [],
        state.equipments = {
            armors: [],
            shields: [],
            weapons: [],
            items: []
        },
        state.armorPenality = 0,
        state.sorcery = false,
        state.spells = [],
        state.enchants = [],
        state.maxSpells = 3,
        state.maxEnchants = 1,
        state.geo = 30,
        state.lists = {
            genders: []
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}