import api from "@/utils/axios";
import store from '@/store/'

const state = () => ({
    spellList: [],
    spellListSelected: false,
    spellListInfo: [],
    enchants: [],
    enchantInfo: []
})

const getters = {

}

const actions = {
    async getEnchantInfo ({ commit }, id) {
        const enchant = await api.get('/enchants/?id=' + id)
        commit('setEnchantInfo', enchant.data.results)
    },
    async getSpellInfo ({ commit }, id) {
        const spell = await api.get('/spells/?id=' + id)
        commit('setSpellInfo', spell.data.results)
    },
    async getEnchants({ commit }) {
        const enchants = await api.get('/enchants/')
        commit('setEnchants', enchants.data.results)     
    },
    chooseEnchant({ commit }, enchant) {
        commit('setChooseEnchant', enchant)
    },       
    deleteEnchant({ commit }) {
        commit('setDeleteEnchant')
    },
    async getSpells ({ commit }, affinity) {
        const getAffinity = await api.get('/afinitys/?name=' +affinity)
        const spells = await api.get('/spells/?afinity=' +getAffinity.data.results[0].id)
        commit('setSpells', spells.data.results)
    },
    getSpellListState ({ commit }, spellState) {
        commit('setSpellListState', spellState)
    },
    async getSpellListInfo({ commit }, id) {
        const spell = await api.get('/spells/?id=' +id)
        commit('setSpellListInfo', spell.data.results)     
    },
    learnSpell({ commit }, values) {
        commit('setLearnSpell', values)
    },
    deleteSpell({ commit }, spell) {
        commit('setDeleteSpell', spell)
    }
}

const mutations = {
    setEnchantInfo (state, enchant) {
        if (store.state.infoScreen.id === enchant[0].id) {
            store.state.infoScreen.id = 0
            store.state.infoScreen.info = []
            store.state.infoScreen.show = false
            store.state.infoScreen.type = ''
        } else {
            store.state.infoScreen.id = enchant[0].id
            store.state.infoScreen.info = enchant[0]
            store.state.infoScreen.show = true
            store.state.infoScreen.type = 'enchant'
        }
    },
    setSpellInfo (state, spell) {
        if (store.state.infoScreen.id === spell[0].id) {
            store.state.infoScreen.id = 0
            store.state.infoScreen.info = []
            store.state.infoScreen.show = false
            store.state.infoScreen.type = ''
        } else {
            store.state.infoScreen.id = spell[0].id
            store.state.infoScreen.info = spell[0]
            store.state.infoScreen.show = true
            store.state.infoScreen.type = 'spell'
        }
    },
    setEnchants (state, enchants) {
        state.enchants = enchants
    },
    setChooseEnchant (state, enchant) {
        store.state.charactersCreate.enchants.push(enchant)
        store.state.charactersCreate.maxEnchants = store.state.charactersCreate.maxEnchants -1
    },
    setDeleteEnchant () {
        store.state.charactersCreate.enchants = []
        store.state.charactersCreate.maxEnchants = store.state.charactersCreate.maxEnchants +1
    },
    setSpells(state, spells) {
        let spellList = []

        const characterSpells = store.state.charactersCreate.spells

        if (characterSpells.length > 0) {
            for (let spell in spells) {
                const findSpell = characterSpells.find(el => el.id === spells[spell].id)

                if (!findSpell) {
                    spellList.push(spells[spell])
                }
            }
        } else {
            spellList = spells
        }

        state.spellList = spellList
    },
    setSpellListState (state, spellState) {
        state.spellListSelected = spellState
    },
    setSpellListInfo (state, spell) {
        state.spellListInfo = spell
    },
    setLearnSpell (state, values) {
        store.state.charactersCreate.spells.push(values.spell)
        store.state.charactersCreate.maxSpells = store.state.charactersCreate.maxSpells -1 
    },
    setDeleteSpell (state, spell) {
        store.state.charactersCreate.spells.splice(store.state.charactersCreate.spells.findIndex(index => index.id === spell), 1)
        store.state.charactersCreate.maxSpells = store.state.charactersCreate.maxSpells +1 
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}