<script>
    // Import components
    import Section from '@/components/template/Section.vue'
    import Divider from '@/components/template/Divider.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'CharactersShow',
        components: {
            Section, Divider
        },
        created() {
            this.$store.dispatch('characters/showCharacter', this.$route.params.id)
        },
        computed: {
            ...mapState({
                userId: state => state.login.userId,
                loading: state => state.characters.loadingShowCharacter,
                showCharacter: state => state.characters.showCharacter
            })
        },
        methods: {
            back() {
                this.$router.push("/characters")              
            },
            getSkillInfo(id) {
                this.$store.dispatch('skills/getSkillInfo', id)
            },
            getAbilityInfo(id) {
                this.$store.dispatch('abilities/getAbilityInfo', id)
            },
            getEquipmentInfo(equipment) {
                this.$store.dispatch('equipments/getEquipmentInfo', equipment)
            },
            getEnchantInfo(id) {
                this.$store.dispatch('spells/getEnchantInfo', id)
            },
            getSpellInfo(id) {
                this.$store.dispatch('spells/getSpellInfo', id)
            }
        }
    }
</script>

