<template>
    <div class="affinity">
        <p>{{ name }} {{ affinityLevel }}</p> 
        <div class="affinity-area">
            <div class="affinity-level" v-for="value in maxAffinityLevel" :key="value">
                <a v-if="affinityLevel >= value" @click="selectAffinityLevel(attribute, affinity, value)" class="selected"></a>
                <a v-else-if="affinityPoints < value" @click="noAffinityPoints()" class="disable"></a>
                <a v-else @click="selectAffinityLevel(attribute, affinity, value)"></a>
            </div>
        </div> 
    </div>    
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: 'Affinity',
        props: {
            name: {
                type: String,
                required: true
            },
            attribute: {
                type: String,
                required: true
            },
            affinity: {
                affinity: String,
                default: false
            },           
            affinityLevel: {
                type: Number,
                required: true
            },
            maxAffinityLevel: {
                type: Number,
                required: true
            },
            affinityPoints: {
                type: Number,
                required: true
            }
        },
        computed: {
            ...mapState({
                // Attributes
                body: state => state.charactersCreate.body,
                mind: state => state.charactersCreate.mind,
                ether: state => state.charactersCreate.ether,
                
                // Affinities
                agility: state => state.charactersCreate.agility,
                dexterity: state => state.charactersCreate.dexterity,
                strength: state => state.charactersCreate.strength,
                stamina: state => state.charactersCreate.stamina,
                cunning: state => state.charactersCreate.cunning,
                influence: state => state.charactersCreate.influence,
                perception: state => state.charactersCreate.perception,
                will: state => state.charactersCreate.will,
                water: state => state.charactersCreate.water,
                fire: state => state.charactersCreate.fire,
                air: state => state.charactersCreate.air,
                earth: state => state.charactersCreate.earth,
                moon: state => state.charactersCreate.moon,
                nature: state => state.charactersCreate.nature,
                light: state => state.charactersCreate.light,
                darkness: state => state.charactersCreate.darkness
            })
        },
        methods: {
            selectAffinityLevel(attribute, affinity, level) {
                this.$store.dispatch('charactersCreate/selectAffinityLevel', { attribute, affinity, level })  
            },
            noAffinityPoints() {
                alert('Você não tem mais pontos para distribuir entre essas afinidades')
            }
        }
    }
</script>

<style scoped>
    .affinity {
        align-items: center;
        border-bottom: 2px solid #e5e5e5;
        display: flex;
        justify-content: space-between;
        margin: 8px 0;
        padding-bottom: 12px;
        width: 100%;
    }
    .affinity p {
        font-size: 20px;
    }
    .affinity-area {
        display: flex;
    }
    .affinity-level {
        align-items: center;
        border: 1px solid #333;
        cursor: pointer;
        display: flex;
        height: 32px;
        justify-content: center;
        margin: 0 4px;
        width: 32px;
    }
    .affinity-level a {
        display: block;
        height: 30px;
        width: 30px;
    }
    .affinity-level:hover {
        background-color: rgba(130, 117, 166, 0.1);
    }
    .affinity-level .selected {
        background-color: #8275A6;
    }
    .affinity-level .disable {
        background-color: #e5e5e5;
    }
</style>
