<template>
    <div v-if="skills.length > 0">
        <div class="skill-element-row">
            <div class="skill-element-header">
                <p>Perícia</p>
                <p>Afinidade</p>
                <p>Especialização</p>
                <p>Bônus Total</p>
                <p>Treinamento</p>
                <p>Informações</p>
            </div>

            <div v-for="skill in orderedSkillList()" :key="skill.id" class="skill-element-item">
                <p v-if="skill.source_skill">
                    <strong>{{ skill.name }}</strong>
                </p>
                <p v-else>{{ skill.name }}</p>

                <p>{{ skill.affinity_label }}</p>

                <p v-if="skill.specialization.length != 0">{{ skill.specialization.name }}</p>
                <p v-else> - </p>

                <p>+ {{ getAttributeLevel(skill.attribute) + getAffinityLevel(skill.affinity) + skill.training_level }}</p>

                <p class="skill-element-item-training">
                    <span class="material-icons minus" @click="decreaseTraining(skill.id)">remove_circle_outline</span>
                    
                    {{ getSkillLevel(skill.id) }}
                    
                    <span v-if="spentSkillPoints < skillPoints" class="material-icons plus" @click="increaseTraining(skill.id, skill.attribute, skill.affinity)">add_circle_outline</span>
                    <span v-else class="material-icons plus disable">add_circle_outline</span> 

                    [ Máximo + {{ getMaxTraining(skill.attribute, skill.affinity) }} ]
                </p>

                <p class="skill-element-item-details">
                    <span class="material-icons info" @click="getSkillInfo(skill.id)">info</span>
                </p>
            </div>
        </div>

        <p class="source-skill-info">* Perícias em negrito foram recebidas pela sua origem.</p>
    </div>

    <div v-else class="not-skills" >
        <p>Você ainda não treinou nenhuma perícia.</p>
    </div>
</template>

<script>
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'SkillElement',
        data() {
            return {
                skillActionState: false
            }
        },
        computed: {
            ...mapState({
                mind: state => state.charactersCreate.mind,
                cunning: state => state.charactersCreate.cunning,
                skills: state => state.charactersCreate.skills,
                skillDetail: state => state.charactersCreate.skillDetail,
                armorPenality: state => state.charactersCreate.armorPenality,
                spentSkillPoints: state => state.charactersCreate.spentSkillPoints,
            }),
            skillPoints() {
                return (this.mind *5) + this.cunning
            }
        },
        methods: {
            orderedSkillList() {
                return this.skills.sort(function (a, b) {
                    if (a.name > b.name) {
                        return 1;
                    }
                    if (a.name < b.name) {
                        return -1;
                    }

                    return 0;
                });
            },
            getAttributeLevel(attribute) {
                return this.$store.state.charactersCreate[attribute]
            },
            getAffinityLevel(affinity) {
                return this.$store.state.charactersCreate[affinity]
            },
            getSkillLevel(id) {
                const skill = this.skills.find(skill => skill.id === id)

                if(skill) return skill.training_level

                return 0
            },
            getMaxTraining(attribute, affinity) {
                let attributeLevel = this.$store.state.charactersCreate[attribute]
                let affinityLevel = this.$store.state.charactersCreate[affinity]

                return attributeLevel + affinityLevel
            },
            getSkillInfo(id) {
                this.$store.dispatch('skills/getSkillInfo', id)
            },
            increaseTraining(id, attribute, affinity) {
                const currentSkill = this.skills.find(skill => skill.id === id)
                const maxSkillLevel = this.getMaxTraining(attribute, affinity)

                if (maxSkillLevel === currentSkill.training_level) {
                    alert('Você atingiu o nível máximo de treinamento permitido nessa perícia.')
                } else {
                    currentSkill.training_level = currentSkill.training_level +1
                    this.$store.dispatch('skills/spentSkillPoints')
                }
            },
            decreaseTraining(id) {
                const currentSkill = this.skills.find(skill => skill.id === id)
                
                if (currentSkill.training_level === 1) {
                    if (currentSkill.source_skill === true) {
                        alert('Você não pode destreinar uma perícia recebida pela sua origem')
                    } else {
                        if (confirm('Deseja destreinar essa perícia?')) {
                            this.$store.dispatch('skills/addSkillPoints')
                            this.$store.state.skillDetail = []

                            const getIndex = this.skills.indexOf(currentSkill.id)
                            this.skills.splice(getIndex, 1)
                        }
                    } 
                } else {
                    currentSkill.training_level = currentSkill.training_level -1
                    this.$store.dispatch('skills/addSkillPoints')
                }                
            }
        }
    }
</script>

