<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Motivações" />

        <div class="motivations-row">
            <div class="motivations-choice">
                <div class="motivations-card" v-for="motivation in motivationList" :key="motivation.id" @click="getMotivationInfo(motivation.id)">
                    <h2>{{ motivation.name }}</h2>
                </div>
            </div>
            
            <div class="motivations-info" v-show="motivationSelected == true">
                <div v-for="motivation in motivationInfo" :key="motivation.id">
                    <h2>{{ motivation.name }}</h2>

                    <p>{{ motivation.description }}</p>

                    <p><strong>Ímpeto.</strong> {{ motivation.impulse.description }}</p>

                    <button @click.prevent="motivationSave(motivation)">Escolher {{ motivation.name }}</button>
                </div>
            </div>

            <div class="motivations-info" v-show="motivationSelected == false">   
                <h2>Motivações</h2>

                <p>A motivação é algo mais inato, mais ligado com seu comportamento guia. Motivação é quem você é como pessoa.</p>
                <p>No 1º nível, quando cria seu personagem, você escolhe uma motivação. Esta escolha pode ser alterada no futuro, ver página X.</p>                 

                <h3>Características das Motivações</h3>

                <ul>
                    <li><strong>Impeto.</strong> Um ímpeto relacionado com aquela motivação.</li>
                </ul>                
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    // Import components
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Section from '@/components/template/Section.vue'
    
    // Import packages
    import { mapState } from 'vuex'

    export default {
        name: 'MotivationsScreen',
        components: {
            ChoiceScreen, Section
        },
        created() {
            if (this.motivationList.length === 0) {
                this.$store.dispatch('motivations/getMotivationList')
            }      
        },
        computed: {
            ...mapState({
                motivationInfo: state => state.motivations.motivationInfo,
                motivationSelected: state => state.motivations.motivationSelected,
                motivationList: state => state.motivations.motivationList,
            })
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('motivations/getMotivationState', false)
                document.body.style.overflow = "visible";
            },
            getMotivationInfo(id) {
                this.$store.dispatch('motivations/getMotivationInfo', id)
                this.$store.dispatch('motivations/getMotivationState', true)
            },
            motivationSave(motivation) {
                this.$store.dispatch('motivations/getCharacterMotivation', motivation)
                this.$router.push("/create-character")
            }
        }
    }
</script>

